/**
 * 
 * @author wangjiegj<wangjiegj@inspur.com>
 */

/**
 * 
 */
enum BaseDataType {
    /**
     * 
     */
    String = 'String',

    /**
     * 
     */
    Text = 'Text',

    /**
     * 
     */
    Integer = 'Integer',

    /**
     * 
     */
    Decimal = 'Decimal',

    /**
     * 
     */
    Boolean = 'Boolean',

    /**
     * 
     */
    Date = 'Date',

    /**
     * 
     */
    DateTime = 'DateTime',

    /**
     * 
     */
    Binary = 'Binary'
}

export { BaseDataType };


export const BaseDataTypes:
    Array<{
        text: string, value: BaseDataType, canChangeLength: boolean, canChangePresicion: boolean, length: number, precision: number
    }> = [
        {
            text: '', value: BaseDataType.String, canChangeLength: true,
            canChangePresicion: false, length: 36, precision: 0
        },
        {
            text: '', value: BaseDataType.Text, canChangeLength: true,
            canChangePresicion: false, length: 1024, precision: 0
        },
        {
            text: '', value: BaseDataType.Integer, canChangeLength: false,
            canChangePresicion: false, length: 0, precision: 0
        },
        {
            text: '', value: BaseDataType.Decimal, canChangeLength: true,
            canChangePresicion: true, length: 18, precision: 0
        },
        {
            text: '', value: BaseDataType.Boolean, canChangeLength: false,
            canChangePresicion: false, length: 1, precision: 0
        },
        {
            text: '', value: BaseDataType.Date, canChangeLength: false,
            canChangePresicion: false, length: 0, precision: 0
        },
        {
            text: '', value: BaseDataType.DateTime, canChangeLength: false,
            canChangePresicion: false, length: 0, precision: 0
        },
        {
            text: '', value: BaseDataType.Binary, canChangeLength: false,
            canChangePresicion: false, length: 0, precision: 0
        },
    ];

export const ComplexDataTypes:
    Array<{
        text: string, value: BaseDataType | string, canChangeLength: boolean, canChangePresicion: boolean, length: number, precision: number
    }> = [
        {
            text: '', value: BaseDataType.String, canChangeLength: true,
            canChangePresicion: false, length: 36, precision: 0
        },
        {
            text: '', value: BaseDataType.Text, canChangeLength: true,
            canChangePresicion: false, length: 1024, precision: 0
        },
        {
            text: '', value: BaseDataType.Integer, canChangeLength: false,
            canChangePresicion: false, length: 0, precision: 0
        },
        {
            text: '', value: BaseDataType.Decimal, canChangeLength: true,
            canChangePresicion: true, length: 18, precision: 0
        },
        {
            text: '', value: BaseDataType.Boolean, canChangeLength: false,
            canChangePresicion: false, length: 1, precision: 0
        },
        {
            text: '', value: BaseDataType.Date, canChangeLength: false,
            canChangePresicion: false, length: 0, precision: 0
        },
        {
            text: '', value: BaseDataType.DateTime, canChangeLength: false,
            canChangePresicion: false, length: 0, precision: 0
        },
        {
            text: '', value: BaseDataType.Binary, canChangeLength: false,
            canChangePresicion: false, length: 0, precision: 0
        },
        {
            text: '', value: 'UnifiedDataType', canChangeLength: true,
            canChangePresicion: true, length: 36, precision: 0
        },
    ];






