import { Component, OnInit, Input, ViewChild } from '@angular/core';
import { SimpleUdtDesignerComponent } from '../simple-udt-designer/simple-udt-designer.component';
import { ViewComplexDataTypeDef } from '../service/udt-reference';
import { ElementCollectionComponent } from './element-collection/element-collection.component';
import { ComplexValidationTabComponent } from './complex-validation-tab/complex-validation-tab.component';
import { DeterminationsTabComponent } from './determinations-tab/determinations-tab.component';

@Component({
  selector: 'app-complex-udt-designer',
  templateUrl: './complex-udt-designer.component.html',
  styleUrls: ['./complex-udt-designer.component.css']
})
export class ComplexUdtDesignerComponent extends SimpleUdtDesignerComponent {

  @ViewChild('eleCollection') eleCollection: ElementCollectionComponent;
  @ViewChild('validations') validations: ComplexValidationTabComponent;
  @ViewChild('determinations') determinations: DeterminationsTabComponent;

  /**
   * Tab-()
   */
  @Input() viewComplexUdt: ViewComplexDataTypeDef;

  tabNameElementCollection = '';
  tabNameDetermination = '';

  constructor() {
    super();
  }

  // -----------------------------------------------------------------------------

  /**
   * [] 
   */
  addElement() {
    if (!this.checkOut()) {
      return;
    }

    this.eleCollection.addHandler();
  }

  /**
   * [] 
   */
  deleteElement() {
    if (!this.checkOut()) {
      return;
    }

    this.eleCollection.removeSelectedElement();
  }

  // -----------------------------------------------------------------------------

  addValidation() {
    this.validations.addValidation();
  }

  removeValidation() {
    this.validations.remove();
  }

  // -----------------------------------------------------------------------------

  addDetermination() {
    this.determinations.addDetermination();
  }

  removeDetermination() {
    this.determinations.remove();
  }

  // -----------------------------------------------------------------------------
  /**
   * 
   */
  checkOut(): boolean {
    return true;
  }
}



