import { Injectable } from '@angular/core';
import { Http } from '@angular/http';
import { Observable } from 'rxjs/Observable';
import { HttpUrlConst } from './udt-reference';
import { UdtGuideInfo } from '../ref-modules/udt-designer-guide/udt-designer-guide.component';


/**
 * udt
 */
@Injectable()
export class UdtInitService {

  constructor(private http: Http) {
  }

  private initUrl = `${HttpUrlConst.currentUrl}/api/dev/main/v1.0/udt/initial`;

  /**
   * udtudt
   */
  init(beGuideInfo: UdtGuideInfo): Observable<any> {
    return this.http.put(this.initUrl, beGuideInfo);
  }
}



