import { GSP, Package, PackageModule } from '@farris/ide-devkit';

import { ComponentFactoryResolver, Injector, Type } from '@angular/core';
import { UdtDesingerFrameComponent } from './views/udt-designer-frame/udt-desinger-frame.component';
import { UdtDesignerPackageView, UdtDesignerGuidePackageView } from './views';
import { UdtDesignerGuideComponent } from './views/udt-designer-frame/ref-modules/udt-designer-guide/udt-designer-guide.component';
import { MetadataDto } from '@gsp-lcm/metadata-selector';
import { Guid } from './views/udt-designer-frame/service/libs';
import { isNullOrUndefined } from 'util';
import { MetadataTypeName } from './views/udt-designer-frame/service/metadata-type';

export class UdtDesignerPackage extends Package {

  private layoutPanel: any;
  private udttype = MetadataTypeName.UnifiedDataType;

  constructor(
    gsp: GSP,
    private cfr: ComponentFactoryResolver,
    private injector: Injector
  ) {
    super(gsp);
  }

  activate(state) {
    this.gsp.workspace.addOpener((uri: string) => this.openUdt(uri));
    this.gsp.eventBus.on(null, null, 'Command:openMetadataGuide:' + this.udttype, this, (data) => this.openUdtGuide(state, data));
  }

  openUdt(uri) {
    if (uri.endsWith('.udt')) {
      const pkgView = new UdtDesignerPackageView({}, this.cfr, this.injector);
      const currentElement = pkgView.getElement().instance as UdtDesingerFrameComponent;
      currentElement.metadataUri = uri;
      // urititle
      return {
        getTitle: () => currentElement.getFileNameFromUri(uri),
        getElement: () => pkgView.getElement()
      };
    }
  }

  /**
   * be
   * @param state 
   * @param dto 
   */
  openUdtGuide(state: any, dto: MetadataDto) {
    if (isNullOrUndefined(dto)) {
      dto = this.createMockDto();
    }
    const packageView1 = new UdtDesignerGuidePackageView(state, this.cfr, this.injector);
    const element1 = packageView1.getElement();
    const guide = element1.instance as UdtDesignerGuideComponent;
    this.gsp.eventBus.requestFor('FileTreePanelPackage', 'metadataCreate', 'AccessFileTree', { action: '6' }, (nodeInfo) => {
      if (!nodeInfo.nodePath) {
        return;
      }
      if (nodeInfo.nodeType === 'folder' || nodeInfo.nodeType === 'project') {
        guide.path = nodeInfo.nodePath;
        guide.initNameSpace();
      } else if (nodeInfo.nodeType === 'file') {
        const handledPath = nodeInfo.nodePath.replace(/\\/g, '/');
        guide.path = handledPath.substring(0, handledPath.lastIndexOf('/'));
        guide.initNameSpace();
      } else {
        console.log('unknowed type');
        alert('');
        return;
      }
    });
    guide.createMetadataEvent.subscribe((data) => {
      this.createMetadata(data);
      // this.refreshTree();
      this.openMetadata(data);
    });
    // 
    guide.createMetadataSuccess.subscribe((data) => { this.addNewTreeNode(data); });
    this.showGuide(element1, guide, '', dto);
  }

  /**
   * 
   */
  private createMetadata(data: MetadataDto) {
    this.gsp.eventBus.post(UdtDesignerPackage, this.id, 'Command:CreateMetadata', data);
  }

  /**
   * 
   * @param data
   */
  private openMetadata(data: MetadataDto) {
    const uri = this.getUriFromMetadataDto(data);
    this.gsp.workspace.open(uri);
  }

  /**
   * dtouri
   */
  private getUriFromMetadataDto(dto: MetadataDto): string {
    if (dto) {
      const fileName = dto.fileName;
      const filePath = dto.relativePath;
      return `/${filePath}/${fileName}`;
    }
  }

  addNewTreeNode(nodeInfo) {
    console.log(nodeInfo);
    this.gsp.eventBus.requestFor('FileTreePanelPackage', 'projectCreate', 'AccessFileTree', nodeInfo, () => {
      console.log('event has been deal done');
    });
  }

  /**
   * 
   */
  refreshTree() {
    this.gsp.eventBus.requestFor('FileTreePanelPackage', 'metadataCreate', 'AccessFileTree', { action: '5' }, () => {
      console.log('refresh tree event has been deal done');
    });
  }

  /**
   * be
   * @param ele
   * @param guide
   * @param title
   * @param dto
   */
  showGuide(ele: any, guide: UdtDesignerGuideComponent, title: string, dto: MetadataDto) {
    guide.metadataContent = dto;
    const modalPanel = this.gsp.workspace.addModalPanel({
      item: {
        element: ele,
        modalOptions: {
          title: title,
          width: 440,
          height: 180,
          buttons: guide.btns
        }
      }
    });
    guide.closeModal.subscribe(() => modalPanel.destroy());
  }

  /**
   * dto
   */
  createMockDto(): MetadataDto {
    const id = Guid.newGuid();
    const code = 'TestUdt' + id.substring(1, 2);
    const name = 'Demo';
    const namespace = 'Inspur.Bef.TestUdt';
    const bizObjId = 'address';
    const relativePath = 'mybe/wjjTest3/udt';
    const dto = new MetadataDto(id, namespace, code, name, code + '.udt', this.udttype, bizObjId, relativePath, '', '');
    return dto;
  }
}



