import { Validators } from '@angular/forms';
import { isNullOrUndefined } from 'util';
import {
  ColumnMapType, ColumnMapTypes, ComplexDataTypeConst, ComplexDataTypeDef
} from '../../service/udt-reference';
import { Component } from '@angular/core';
import { BaseCardComponent } from '../../basic-info-tab/base-form';
import { ViewUdtDbInfo } from '../../service/libs/ViewModel/src/viewComlexDataTypeDef/viewUdtDbInfo';

@Component({
  selector: 'app-complex-basic-info-tab',
  templateUrl: './complex-basic-info-tab.component.html',
  styleUrls: ['./complex-basic-info-tab.component.css']
})
export class ComplexBasicInfoTabComponent extends BaseCardComponent {

  /**
   * 
   */
  columnMapType: ColumnMapType;
  columnMapTypes = ColumnMapTypes;
  data: ComplexDataTypeDef;

  /**
   * 
   */
  protected createForm() {
    this.formGroup = this.formBuilder.group({
      code: ['', Validators.required],
      name: ['', Validators.required],
      creator: [''],
      createdDate: [''],
      modifier: [''],
      modifiedDate: [''],
      assemblyName: [''],
      description: ''
    });
  }

  /**
   * 
   * @param udt
   */
  protected setFormValue(udt: ComplexDataTypeDef) {
    this.unsubscribeFormGroupValueChanges();
    if (isNullOrUndefined(udt)) {
      this.clearFormValue();
    }
    if (isNullOrUndefined(this.formGroup)) {
      this.createForm();
    }
    this.formGroup.patchValue({
      code: udt.Code || '',
      name: udt.Name || '',
      creator: udt.Creator || '',
      createdDate: udt.CreatedDate || '',
      modifier: udt.Modifier || '',
      modifiedDate: udt.ModifiedDate || '',
      description: udt.Description || ''
    });
    // mapType
    this.columnMapType = udt.DbInfo.MappingType;

    this.formGroup.enable();
    this.formGroup.controls.creator.disable();
    this.formGroup.controls.createdDate.disable();
    this.formGroup.controls.modifier.disable();
    this.formGroup.controls.modifiedDate.disable();
    this.subscribeFormGroupValueChanges();
  }

  /**
   * 
   */
  protected clearFormValue() {
    this.unsubscribeFormGroupValueChanges();
    if (isNullOrUndefined(this.formGroup)) {
      this.createForm();
    }
    this.formGroup.disable();
    this.formGroup.patchValue({
      code: '',
      name: '',
      creator: '',
      createdDate: '',
      modifier: '',
      modifiedDate: '',
      assemblyName: '',
      description: ''
    });
    this.columnMapType = null;
    this.subscribeFormGroupValueChanges();
  }

  /**
   * 
   */
  protected subscribeFormGroupValueChanges() {
    const subscriptionCode = this.formGroup.controls.code.valueChanges.subscribe(code => {
      this.currentViewModel.code = code;
    });
    const subscriptionName = this.formGroup.controls.name.valueChanges.subscribe(name => this.currentViewModel.name = name);
    const subscriptionDescription = this.formGroup.controls.description.valueChanges.subscribe(description =>
      this.currentViewModel.description = description);
    if (isNullOrUndefined(this.formGroupSubscriptions)) {
      this.formGroupSubscriptions = new Array<any>();
    }
    this.formGroupSubscriptions.push(subscriptionCode);
    this.formGroupSubscriptions.push(subscriptionName);
    this.formGroupSubscriptions.push(subscriptionDescription);
  }

  /**
   * 
   */
  selectcolumnMapType(newMappingType: ColumnMapType) {
    this.currentViewModel.update(ComplexDataTypeConst.DbInfo, dbInfo => {
      (<ViewUdtDbInfo>dbInfo).MappingType = newMappingType;
      return dbInfo;
    });
  }
}



