import { UdtObjectType } from '../EnumType/UdtObjectType';
import { UdtAssociation } from '../Association/UdtAssociation';
import { UdtEnumValue } from '../Enum/UdtEnumValue';
import { UnifiedDataTypeDef } from '../UnifiedDataTypeDef';
import { BaseDataType } from '../EnumType/BaseDataType';
import { Guid } from '../../../util/Guid';
import { GspAssociation } from '../../../cef';
import { GSPEnumValue } from '@gsp-bef/gsp-be-metadata';

/**
 * 
 * @author wangjiegj<wangjiegj@inspur.com>
 */


/**
 * 
 */
export class UdtElement {

    constructor() {
        this.Id = Guid.newGuid();
        this.Length = 36;
        this.Precision = 0;
        this.DataType = BaseDataType.String;
        this.ObjectType = UdtObjectType.None;
        this.IsMultiLanguage = false;
        this.IsRequire = false;
    }

    /**
     * 
     */
    Id: string;

    /**
     * 
     */
    Code: string;

    /**
     * 
     */
    Name: string;

    /**
     * 
     */
    public DefaultValue: string;

    /**
     * 
     */
    LabelId: string;

    /**
     * Id
     */
    RefElementId: string;

    /**
     * (string)
     */
    public DataType: BaseDataType = BaseDataType.String;

    /**
     * 
     */
    public Length: number;

    /**
     * 
     */
    public Precision: number;

    /**
     * 
     */
    public IsUnique: boolean;

    /**
     * 
     */
    public IsRequire: boolean;

    /**
     * 
     */
    IsMultiLanguage: boolean;

    /**
     * (None)
     */
    public ObjectType: UdtObjectType = UdtObjectType.None;

    /**
     * 
     */
    public ChildAssociations: GspAssociation[];

    /**
     * 
     */
    public ContainEnumValues: GSPEnumValue[];

    /**
     * 
     */
    IsUdt: boolean;

    /**
     * 
     */
    UdtPkgName: string;

    /**
     * ID
     */
    UdtID: string;

    /**
     * ()
     */
    UnifiedDataType: UnifiedDataTypeDef;

    /**
     * udt
     */
    UdtName: string;

    /**
     * 
     */
    ChildElements: UdtElement[];

    /**
     * 
     */
    MappingRelation: any[];
}



