  //   const index = this.metadataService.metadatas.length;
  //   // Load
  //   if (this.metadataService.metadatas[index - 1].treenode.PackageName === 'default') {
  //     this.metadataService.LoadMetadata().subscribe(templates => {
  //       const metadata4Ref = new Metadata4RefDto();
  //       metadata4Ref.metadata = templates;
  //       metadata4Ref.packageHeader = new MetadataPackageHeader();
  //       this.gspmetadata = metadata4Ref;
  //       this.currentBeMetadata.next(metadata4Ref);
  //       this.closeWindow.next(true);
  //     });
  //   } else { // GetMetadataFromPackage
  //     this.metadataService.GetMetadataFromPackage().subscribe(templates => {
  //       this.gspmetadata = templates;
  //       this.currentBeMetadata.next(templates);
  //       this.closeWindow.next(true);
  //     });
  //   }
  //   // 
  //   this.metadataService.clearMetadata();
  //   this.isExcuteCancel = false;
  //   this.hideModal();
  //   this.isExcuteCancel = true;
  // }
  // // 
  // PackageSelect() {
  //   this.metadatapackageselectComponent.showModal();
  // }

  // showModal(): void {
  //   this.isModalShown = true;
  // }

  // hideModal(): void {
  //   this.autoShownModal.hide();
  //   if (this.isExcuteCancel) {
  //     this.closeWindow.next(true);
  //   }
  // }

  // onHidden(): void {
  //   this.isModalShown = false;
  // }

