import {
    SimpleDataTypeDef, Guid, UseTypeInfo, SimpleDataTypeConst, UseType, UnifiedDataTypeDef, UdtObjectType, UdtElement
} from './udt-reference';
import { ComplexDataTypeDef } from './libs/UnifiedDataType/src/ComplexDataTypeDef';
import { UdtDbInfo, ColumnMapType } from './libs';

class InitUdtForTest {

    constructor() {
    }
    static InitComplexUdt(): ComplexDataTypeDef {
        const cUdt = new ComplexDataTypeDef();
        cUdt.DbInfo = new UdtDbInfo();
        cUdt.DbInfo.MappingType = ColumnMapType.MultiColumns;
        const sUdt1 = this.InitUdtElement();
        sUdt1.Code = 'sUdt1';
        sUdt1.Name = '1';
        sUdt1.ObjectType = UdtObjectType.None;
        const sUdt2 = this.InitUdtElement();
        sUdt2.Code = 'sUdt2';
        sUdt2.Name = '2';
        sUdt2.ObjectType = UdtObjectType.Association;
        const sUdt3 = this.InitUdtElement();
        sUdt3.Code = 'sUdt3';
        sUdt3.Name = '3';
        sUdt3.ObjectType = UdtObjectType.Enum;
        this.AddUnifiedDataTypeProperties(cUdt);
        cUdt.Elements = new Array<UdtElement>();
        cUdt.Elements.push(...[sUdt1, sUdt2, sUdt3]);
        return cUdt;
    }

    static InitUdtElement(): UdtElement {
        const data = new UdtElement();
        return data;
    }
    static InitSimpleUdt(): SimpleDataTypeDef {
        const data = new SimpleDataTypeDef();
        this.AddUnifiedDataTypeProperties(data);
        this.AddUseTypeInfos(data);
        this.AddExtendProperties(data);
        return data;
    }

    static AddUnifiedDataTypeProperties(data: UnifiedDataTypeDef) {
        data.Id = Guid.newGuid();
        const code = 'udtForTest';
        data.Code = code;
        data.Name = code;
        data.Creator = 'wangjiegj';
        data.CreatedDate = '2018619';
        data.Modifier = 'wangjiegj2';
        data.ModifiedDate = '2018619';
        data.Description = '';
        return data;
    }

    static AddUseTypeInfos(data: SimpleDataTypeDef) {
        const infos = new Array<UseTypeInfo>();
        infos.push(this.initUseTypeInfo(SimpleDataTypeConst.DataType, UseType.AsConstraint, false));
        infos.push(this.initUseTypeInfo(SimpleDataTypeConst.Length, UseType.AsConstraint, false));
        infos.push(this.initUseTypeInfo(SimpleDataTypeConst.Precision, UseType.AsConstraint, false));
        infos.push(this.initUseTypeInfo(SimpleDataTypeConst.DefaultValue, UseType.AsTemplate, true));
        infos.push(this.initUseTypeInfo(SimpleDataTypeConst.IsUnique, UseType.AsTemplate, true));
        infos.push(this.initUseTypeInfo(SimpleDataTypeConst.IsRequired, UseType.AsTemplate, true));
        infos.push(this.initUseTypeInfo(SimpleDataTypeConst.ObjectType, UseType.AsTemplate, true));
        infos.push(this.initUseTypeInfo(SimpleDataTypeConst.ChildAssociations, UseType.AsTemplate, true));
        infos.push(this.initUseTypeInfo(SimpleDataTypeConst.ContainEnumValues, UseType.AsTemplate, true));
        data.PropertyUseTypeInfos = infos;
        return data;
    }

    static initUseTypeInfo(propertyName: string, useType: UseType, canEdit: boolean): UseTypeInfo {
        const info = new UseTypeInfo();
        info.PropertyName = propertyName;
        info.PropertyUseType = useType;
        info.CanEdit = canEdit;
        return info;
    }

    static AddExtendProperties(data: SimpleDataTypeDef) {
        data.Length = 36;
        data.Precision = 0;
        data.DefaultValue = 'defaultValue';
        data.IsRequired = true;
        data.IsRequired = true;
        return data;
    }
}

export const TemporarySimpleData: SimpleDataTypeDef = InitUdtForTest.InitSimpleUdt();
export const TemporaryComplexData: ComplexDataTypeDef = InitUdtForTest.InitComplexUdt();






