import { List, Map } from 'immutable';
import { MapBase } from '../base/mapBase';
import { isNullOrUndefined } from 'util';
import { ViewAssociationList } from '../viewAssociation/viewAssociationList';
import { UdtObjectType, UdtElement } from '../../../UnifiedDataType';
import { ViewEnumValueList } from '../viewEnumValue/viewEnumValueList';
import { ListBase } from '../base/listBase';
import { SimpleDataTypeConst, UnifiedDataTypeConst, ComplexDataTypeConst } from '../../../const';
import { ViewUnifiedDataTypeDef } from '../viewDataTypeDef/viewUnifiedDataTypeDef';
import { ViewElementList } from '../viewDataTypeDef/viewSimpleDataTypeDef';

export class ViewUdtElement extends MapBase {

  /**
   * 
   * @param element
   */
  constructor(element: UdtElement | Map<string, any>) {
    super(element);
    if (isNullOrUndefined(element)) {
      return;
    }
    this.data = this.getViewElement(this.data);
  }

  /**
   * Map
   * @param element
   */
  private getViewElement(element: Map<string, any>): any {
    const viewElement = element
      .update(SimpleDataTypeConst.ContainEnumValues, enums => this.getEnumValues(enums))
      .update(SimpleDataTypeConst.ChildAssociations, assos => this.getViewAssos(assos))
      .update(ComplexDataTypeConst.ChildElements, childElemenst => this.getChildElements(childElemenst));

    return viewElement;
  }

  private getEnumValues(enums: List<any>): ViewEnumValueList {
    return new ViewEnumValueList(enums);
  }

  private getViewAssos(assos: List<any>): ViewAssociationList {
    const assoList = new ViewAssociationList(assos);
    assoList.refreshView.subscribe(() => {
      this.refreshViewLayer();
    });
    return assoList;
  }

  private getChildElements(childElements: List<any>): ViewElementList {
    const elementList = new ViewElementList(childElements);
    return elementList;
  }

  set code(code: string) {
    this.set(UnifiedDataTypeConst.Code, code);
  }

  get code(): string {
    return this.get(UnifiedDataTypeConst.Code);
  }

  set name(name: string) {
    this.set(UnifiedDataTypeConst.Name, name);
  }

  get name(): string {
    return this.get(UnifiedDataTypeConst.Name);
  }

  set description(description: string) {
    this.set(UnifiedDataTypeConst.Description, description);
  }

  get description(): string {
    return this.get(UnifiedDataTypeConst.Description);
  }

  get assemblyName(): string {
    return this.get(UnifiedDataTypeConst.AssemblyName);
  }

  set assemblyName(validations: string) {
    this.set(UnifiedDataTypeConst.AssemblyName, validations);
  }

  set enumValues(enums: ViewEnumValueList) {
    this.set(SimpleDataTypeConst.ContainEnumValues, enums);
  }

  get enumValues(): ViewEnumValueList {
    return this.get(SimpleDataTypeConst.ContainEnumValues);
  }

  get associations(): ViewAssociationList {
    return this.get(SimpleDataTypeConst.ChildAssociations);
  }

  set associations(assos: ViewAssociationList) {
    this.set(SimpleDataTypeConst.ChildAssociations, assos);
  }

  get objectType(): UdtObjectType {
    return this.get(SimpleDataTypeConst.ObjectType);
  }

  set objectType(type: UdtObjectType) {
    this.set(SimpleDataTypeConst.ObjectType, type);
  }


  clearEnumValues() {
    this.data = this.data.update(SimpleDataTypeConst.ContainEnumValues, () => {
      return new ViewEnumValueList(null);
    });
  }

  clearAssociations() {
    this.data = this.data.update(SimpleDataTypeConst.ChildAssociations, () => {
      return new ViewAssociationList(null);
    });
  }
}


export class ViewUdtElementList extends ListBase<ViewUdtElement> {

  constructor(elements: UdtElement[] | List<any>) {
    super(elements);
    if (elements) {
      this.data = this.getElements(this.data);
    }
  }

  /**
   * List
   * @param elementArray
   */
  private getElements(elementArray: List<any>): List<any> {
    if (isNullOrUndefined(elementArray)) {
      return;
    }

    let viewElementList = elementArray;

    if (elementArray.size > 0) {
      for (let i = 0; i < elementArray.size; i++) {
        viewElementList = viewElementList.update(i, obj => this.getViewElement(elementArray.get(i)));
      }
      return viewElementList;
    }
  }

  /**
   * Map
   * @param element
   */
  protected getViewElement(element: Map<string, any>): any {
    const viewEle = new ViewUdtElement(element);
    return viewEle;
  }
}



