import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { isNullOrUndefined } from 'util';
import { Component, OnInit, Input, OnChanges, OnDestroy, SimpleChanges } from '@angular/core';
import { Subscription } from 'rxjs/Subscription';
import { ViewSimpleDataTypeDef, SimpleDataTypeDef, UnifiedDataTypeDef, ViewUnifiedDataTypeDef } from '../service/udt-reference';

@Component({
    selector: 'app-base',
    templateUrl: './basic-info-tab.component.html',
    styleUrls: ['./basic-info-tab.component.css']
})
export class BaseCardComponent implements OnInit, OnChanges, OnDestroy {

    @Input() currentViewModel: ViewUnifiedDataTypeDef;

    public formGroup: FormGroup;

    data: UnifiedDataTypeDef;

    /**
     * 
     */
    protected refreshSubscriptions = new Array<Subscription>();

    /**
     * 
     * Subscription
     */
    protected formGroupSubscriptions: any[];

    constructor(protected formBuilder: FormBuilder) { }

    ngOnInit() {
    }

    ngOnChanges(changes: SimpleChanges): void {
        if (isNullOrUndefined(changes.currentViewModel.currentValue)) {
            this.createForm();
        } else {
            if (isNullOrUndefined(changes.currentViewModel.previousValue)) {
                this.refresh();
            } else {
                this.refresh();
            }
        }
    }

    ngOnDestroy(): void {
        this.unsubscribeRefreshLayer();
        this.unsubscribeFormGroupValueChanges();
    }

    /**
     * 
     */
    protected refresh() {
        if (isNullOrUndefined(this.currentViewModel)) {
            this.clearFormValue();
            return;
        }

        this.unsubscribeRefreshLayer();
        this.data = this.currentViewModel.toJS();
        this.setFormValue(this.data);
        this.subscribeRefreshViewLayer();
    }

    /**
     * viewAction
     */
    protected subscribeRefreshViewLayer(): any {
        const subscription = this.currentViewModel.refreshView.subscribe(value => {
            this.data = value.toJS();
            this.setFormValue(this.data);
        });
        this.refreshSubscriptions.push(subscription);
    }

    /**
     * viewAction
     */
    protected unsubscribeRefreshLayer() {
        if (this.refreshSubscriptions) {
            while (this.refreshSubscriptions.length > 0) {
                const subscription = this.refreshSubscriptions.pop();
                subscription.unsubscribe();
            }
        }
    }

    /**
     * 
     */
    protected unsubscribeFormGroupValueChanges() {
        if (this.formGroupSubscriptions && this.formGroupSubscriptions.length > 0) {
            while (this.formGroupSubscriptions.length > 0) {
                const subscription = this.formGroupSubscriptions.pop();
                subscription.unsubscribe();
            }
        }
    }

    /**
     * ()
     */
    protected createForm() {
    }

    /**
     * ()
     * @param udt
     */
    protected setFormValue(udt: any) {
    }

    /**
     * ()
     */
    protected clearFormValue() {
    }

    /**
     * ()
     */
    protected subscribeFormGroupValueChanges() {
    }
}



