import { Map } from 'immutable';
import { MapBase } from '../base/mapBase';
import { isNullOrUndefined } from 'util';
import { UdtDbInfo, ColumnMapType } from '../../../UnifiedDataType';
import { SimpleDataTypeConst, ComplexDataTypeConst } from '../../../const';

export class ViewUdtDbInfo extends MapBase {

  /**
   * 
   * @param element
   */
  constructor(element: UdtDbInfo | Map<string, any>) {
    super(element);
  }

  get MappingType(): ColumnMapType {
    return this.get(ComplexDataTypeConst.MappingType);
  }

  set MappingType(value: ColumnMapType) {
    this.set(ComplexDataTypeConst.MappingType, value);
  }
}



