import { Injectable } from '@angular/core';
import { Http } from '@angular/http';
import { Observable } from 'rxjs/Observable';
import { HttpUrlConst } from './udt-reference';
import { UdtElement } from './libs';

/**
 * udtElementudtChildElementsMapping
 */
@Injectable()
export class UdtElementChooseUdtService {

    constructor(private http: Http) {
    }

    currentUrl = HttpUrlConst.currentUrl;

    /**
     * udt
     */
    chooseUdt(refUdtId: string, ele: UdtElement): Observable<any> {
        const refUdtInfo = new UpdateUdtElementInfo();
        refUdtInfo.refUdtId = refUdtId;
        refUdtInfo.path = HttpUrlConst.currentPath;
        refUdtInfo.udtElementJson = JSON.stringify(ele);
        const chooseUdtUrl = `${this.currentUrl}` + `/api/dev/main/v1.0/udt/chooseUdt`;
        return this.http.put(chooseUdtUrl, refUdtInfo);
    }
}

/**
 * jit
 */
class UpdateUdtElementInfo {
    refUdtId: string;
    path: string;
    udtElementJson: string;
}



