import { List } from 'immutable';
import { isNullOrUndefined } from 'util';
import { Subject } from 'rxjs/Subject';
import { ListBase } from '../base/listBase';
import { ViewAssoKey } from './viewAssoKey';
import { UdtAssociationKey } from '../../../UnifiedDataType';

export class ViewAssoKeyList extends ListBase<ViewAssoKey> {

    public confirmAssoKeys: Subject<this> = new Subject<this>();

    constructor(assoKeysArray: UdtAssociationKey[] | List<any>) {
        super(assoKeysArray);
        if (isNullOrUndefined(assoKeysArray)) {
            return;
        }
        this.data = this.getViewAssoKeys(this.data);
    }

    private getViewAssoKeys(assoKeyArray: List<any>): List<ViewAssoKey> {
        if (isNullOrUndefined(assoKeyArray)) {
            return;
        }

        let assoKeyList = assoKeyArray;

        if (assoKeyArray.size > 0) {
            for (let i = 0; i < assoKeyArray.size; i++) {
                assoKeyList = assoKeyList.update(i, () => this.getViewAssociation(assoKeyArray.get(i)));
            }
            return assoKeyList;
        }
    }

    private getViewAssociation(asso: any): any {
        return new ViewAssoKey(asso);
    }
}






