import { MapBase } from '../base/mapBase';
import { UseTypeInfo } from '../../../UnifiedDataType';
import { ListBase } from '../base/listBase';
import { List, Map } from 'immutable';
import { isNullOrUndefined } from 'util';
import { UnifiedDataTypeConst } from '../../../const';

export class ViewUseTypeInfoList extends ListBase<ViewUseTypeInfo> {

    constructor(infoArray: UseTypeInfo[] | List<any>) {
        super(infoArray);
        if (isNullOrUndefined(infoArray)) {
            return;
        }
        this.data = this.getViewUseTypeInfos(this.data);
    }

    getViewUseTypeInfos(infoArray: List<any>): any {
        if (isNullOrUndefined(infoArray)) {
            return;
        }

        let viewEnumValueList = infoArray;

        if (infoArray.size > 0) {
            for (let i = 0; i < infoArray.size; i++) {
                viewEnumValueList = viewEnumValueList.update(i, () => this.getViewUseTypeInfo(infoArray.get(i)));
            }
            return viewEnumValueList;
        }
    }

    getViewUseTypeInfo(info: UseTypeInfo): any {
        return new ViewUseTypeInfo(info);
    }


    getIndexByPropertyName(propertyName: string): number {
        if (isNullOrUndefined(this.data)) {
            return -1;
        }
        const list = this.data;
        for (let i = 0; i < list.size; i++) {
            const viewElement = list.get(i) as MapBase;
            const currentID = viewElement.get(UnifiedDataTypeConst.PropertyName);
            if (propertyName === currentID) {
                return i;
            }
        }
        return -1;
    }

    removeByPropertyName(propertyName: string) {
        const index = this.getIndexByPropertyName(propertyName);
        if (index === -1) {
            // alert('');
            return;
        }
        this.remove(index);
    }

    updateByPropertyName(propertyName: string, updater: any) {
        const index = this.getIndexByPropertyName(propertyName);
        if (index === -1) {
            // alert('');
            return;
        }
        this.update(index, updater);
    }


    setSelectIndexByPropertyName(propertyName: string) {
        const index = this.getIndexByPropertyName(propertyName);
        this.setSelectIndex(index);
    }

    getItemByPropertyName(propertyName: string): MapBase {
        const list = this.data;
        for (let i = 0; i < list.size; i++) {
            const viewElement = list.get(i) as MapBase;
            const currentPropertyName = viewElement.get(UnifiedDataTypeConst.PropertyName);
            if (propertyName === currentPropertyName) {
                const subscription = viewElement.refreshView.subscribe(item => {
                    this.refreshViewLayer();
                });

                return viewElement;
            }
        }
        return null;
    }

}

export class ViewUseTypeInfo extends MapBase {
    /**
     * 
     * @param info
     */
    constructor(info: UseTypeInfo | Map<string, any>) {
        super(info);
    }
}



