import { Component, OnInit, Input, SimpleChanges, ViewChild, OnChanges } from '@angular/core';
import {
  ViewDetermination, GSPBizEntityObject, IGSPCommonElement, BETriggerTimePointType, ListBase,
  IGSPCommonObject, Determination, RequestChildElement, RequestNodeTriggerType, UdtElement, ComplexDataTypeDef
} from '../../../service/udt-reference';
import { isNullOrUndefined } from 'util';
import { GridComponent } from '@progress/kendo-angular-grid';

import { List } from 'immutable';
import {
  TriggerElementsGridComponent
} from '../../complex-validation-tab/complex-validation-card/trigger-elements-grid/trigger-elements-grid.component';

@Component({
  selector: 'app-be-determination-trigger-element-grid',
  templateUrl: './determination-trigger-element-grid.component.html',
  styleUrls: ['./determination-trigger-element-grid.component.css']
})
export class DeterminationTriggerElementGridComponent implements OnInit, OnChanges {

  @ViewChild('grid') grid: GridComponent;
  @ViewChild('editElements') editElements: TriggerElementsGridComponent;
  /**
   * 
   */
  @Input() viewDetermination: ViewDetermination;
  @Input() currentObject: ComplexDataTypeDef;
  @Input() type: BETriggerTimePointType;

  determination: Determination;
  /**
     * 
     */
  // triggerObject: IGSPCommonObject;
  /**
   * 
   */
  triggerElements: UdtElement[];

  constructor() { }

  ngOnInit() {
    this.refresh();
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (changes['viewDetermination']) {
      this.refresh();
    }
  }

  private refresh() {
    if (isNullOrUndefined(this.viewDetermination) || isNullOrUndefined(this.viewDetermination.toJS())) {
      this.clearAllData();
      return;
    }
    this.determination = <Determination>this.viewDetermination.toJS();
    this.initTriggerObjectAndElements(this.determination);
  }

  clearAllData() {
    this.determination = null;
    this.triggerElements = null;
  }

  initTriggerObjectAndElements(dtm: Determination): any {
    if (isNullOrUndefined(dtm)) {
      this.triggerElements = null;
      return;
    }

    // 
    if (!isNullOrUndefined(dtm.RequestElements) && dtm.RequestElements.length > 0) {
      const eleIds = dtm.RequestElements;
      // 
      this.triggerElements = this.initTriggerElementsByIds(this.currentObject, eleIds);
      return;
    }

    this.triggerElements = null;
    return;
  }

  closeGridCell() {
    // 
    this.grid.closeCell();
    // 
    this.grid.cancelCell();
    // 
    this.grid.closeRow(-1);
  }

  private initTriggerElementsByIds(obj: ComplexDataTypeDef, ids: string[]): UdtElement[] {
    if (isNullOrUndefined(obj) || isNullOrUndefined(ids)) {
      return null;
    }
    return obj.Elements.filter(ele => ids.includes(ele.Id));
  }

  public editTriggerElements() {
    if (isNullOrUndefined(this.viewDetermination) || isNullOrUndefined(this.viewDetermination.toJS())) {
      alert('');
      return;
    }
    this.editElements.load(this.triggerElements, this.currentObject);
  }

  // saveHandler(requestKeyValuePair: RequestChildElement) {
  //   if (isNullOrUndefined(requestKeyValuePair)) {
  //     this.viewDetermination.RequestElements = null;
  //     this.viewDetermination.RequestChildElements = null;
  //     this.refresh();
  //     return;
  //   }

  //   const ids = requestKeyValuePair.RequestChildElementValue;

  //   // 
  //   if (isNullOrUndefined(requestKeyValuePair.RequestChildElementKey) || requestKeyValuePair.RequestChildElementKey.length === 0) {
  //     this.viewDetermination.RequestElements = new ListBase(List(ids));
  //     this.viewDetermination.RequestChildElements = null;
  //     this.refresh();
  //     return;
  //   }
  //   // 
  //   this.viewDetermination.RequestChildElements = new ViewRequestChildElementList([requestKeyValuePair]);
  //   this.viewDetermination.RequestElements = null;
  //   this.refresh();
  // }

  /**
   * 
   * @param requestKeyValuePair 
   */
  saveHandler(requestKeyValuePair: RequestChildElement) {
    if (isNullOrUndefined(requestKeyValuePair)) {
      this.viewDetermination.RequestElements = null;
      this.refresh();
      return;
    }

    const ids = requestKeyValuePair.RequestChildElementValue;

    this.viewDetermination.RequestElements = new ListBase(List(ids));
    this.refresh();
  }

  cancelHandler(args: any) {

  }
}



