import { List, Map } from 'immutable';
import { isNullOrUndefined } from 'util';
import { ViewAssociationList } from '../viewAssociation/viewAssociationList';
import { SimpleDataTypeDef, UdtObjectType } from '../../../UnifiedDataType';
import { ViewEnumValueList } from '../viewEnumValue/viewEnumValueList';
import { ViewUnifiedDataTypeDef } from './viewUnifiedDataTypeDef';
import { ListBase } from '../base/listBase';
import { SimpleDataTypeConst } from '../../../const';

export class ViewSimpleDataTypeDef extends ViewUnifiedDataTypeDef {
    ObjectType: UdtObjectType;

    /**
     * 
     * @param element
     */
    constructor(element: SimpleDataTypeDef | Map<string, any>) {
        super(element);
        if (isNullOrUndefined(element)) {
            return;
        }
        this.data = this.getViewElement(this.data);
    }

    /**
     * Map
     * @param element
     */
    private getViewElement(element: Map<string, any>): any {
        const viewElement = element.update(
            SimpleDataTypeConst.ContainEnumValues, enums => this.getEnumValues(enums)
        ).update(SimpleDataTypeConst.ChildAssociations, assos => this.getViewAssos(assos));

        return viewElement;
    }

    private getEnumValues(enums: List<any>): ViewEnumValueList {
        return new ViewEnumValueList(enums);
    }

    private getViewAssos(assos: List<any>): ViewAssociationList {
        const assoList = new ViewAssociationList(assos);
        assoList.refreshView.subscribe(() => {
            this.refreshViewLayer();
        });
        return assoList;
    }

    set enumValues(enums: ViewEnumValueList) {
        this.set(SimpleDataTypeConst.ContainEnumValues, enums);
    }

    get enumValues(): ViewEnumValueList {
        return this.get(SimpleDataTypeConst.ContainEnumValues);
    }

    get associations(): ViewAssociationList {
        return this.get(SimpleDataTypeConst.ChildAssociations);
    }

    set associations(assos: ViewAssociationList) {
        this.set(SimpleDataTypeConst.ChildAssociations, assos);
    }

    get objectType(): UdtObjectType {
        return this.get(SimpleDataTypeConst.ObjectType);
    }

    set objectType(type: UdtObjectType) {
        this.set(SimpleDataTypeConst.ObjectType, type);
    }


    clearEnumValues() {
        this.update(SimpleDataTypeConst.ContainEnumValues, () => {
            return new ViewEnumValueList(null);
        });
    }

    clearAssociations() {
        this.update(SimpleDataTypeConst.ChildAssociations, () => {
            return new ViewAssociationList(null);
        });
    }
}


export class ViewElementList extends ListBase<ViewSimpleDataTypeDef> {

    constructor(elements: SimpleDataTypeDef[] | List<any>) {
        super(elements);
        if (elements) {
            this.data = this.getElements(this.data);
        }
    }

    /**
     * List
     * @param elementArray
     */
    private getElements(elementArray: List<any>): List<any> {
        if (isNullOrUndefined(elementArray)) {
            return;
        }

        let viewElementList = elementArray;

        if (elementArray.size > 0) {
            for (let i = 0; i < elementArray.size; i++) {
                viewElementList = viewElementList.update(i, obj => this.getViewElement(elementArray.get(i)));
            }
            return viewElementList;
        }
    }

    /**
     * Map
     * @param element
     */
    protected getViewElement(element: Map<string, any>): any {
        const viewEle = new ViewSimpleDataTypeDef(element);
        return viewEle;
    }
}



