import { TemplateRef, ViewChild, Component, OnInit, OnChanges, EventEmitter, Input, Output, SimpleChanges } from '@angular/core';
// import { TreeModel } from '../../../../../../packages/file-tree-panel-package/models/tree-model';
import { isNullOrUndefined } from 'util';
import { MetadataDto, GSPMetadataService, HttpUrlConst, UdtMetadataContent } from '../../service/udt-reference';
import { UdtInitService } from '../../service/udt-init.service';
import { UdtTypeConst } from '../../service/libs/const';
// import { TemporaryComplexData } from '../../service/templeData';

@Component({
  selector: 'app-udt-designer-guide',
  templateUrl: './udt-designer-guide.component.html',
  styleUrls: ['./udt-designer-guide.component.css'],
  providers: [UdtInitService]
})
export class UdtDesignerGuideComponent implements OnInit, OnChanges {

  @ViewChild('btn') btns: TemplateRef<any>;
  udtContentWithType: UdtMetadataContent;

  @Input() triggerShowWindow: boolean;
  /**
   * 
   */
  createMetadataEvent: EventEmitter<any> = new EventEmitter<any>();

  /**
   * 
   */
  closeModal = new EventEmitter<any>();

  /**
   * 
   */
  udtTypes: Array<{ text: string, value: string }> = [
    { text: '', value: UdtTypeConst.SimpleDataType },
    { text: '', value: UdtTypeConst.ComplexDataType },
    // { text: '', value: false },
  ];
  udtType = UdtTypeConst.SimpleDataType;

  /**
   * 
   */
  private _metadataContnet: MetadataDto;

  /**
   * 
   */
  createMetadataSuccess: EventEmitter<any> = new EventEmitter<any>();
  path: any;
  projInfo: any;
  metadata = {
    nameSpace: '',
    code: '',
    name: '',
    bizobjectID: '',
    boCode: '',
    location: ''
  };

  set metadataContent(metadataContent: any) {
    this._metadataContnet = metadataContent;
    this.load(metadataContent);
  }

  /**
   * udt
   */
  dependentInfo: UdtGuideInfo = new UdtGuideInfo();

  showGuideWindow = false;

  // constructor(private treeModel: TreeModel, private gspMetadataService: GSPMetadataService, private initBeService: UdtInitService) {
  constructor(private gspMetadataService: GSPMetadataService, private initBeService: UdtInitService) {
    this.createMetadataEvent = new EventEmitter<any>();
  }

  ngOnInit() {
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (!this.showGuideWindow) {
      this.showModal();
    }
  }

  initNameSpace(): any {
    this.metadata.location = this.path.replace(/\\/g, '/');
    this.gspMetadataService.GetProjectInfo(this.path.substring(1)).subscribe(data => {
      this.projInfo = data;
      if (this.projInfo) {
        this.metadata.nameSpace = this.projInfo.nameSpace;
      }
      this.gspMetadataService.GetGspProjInfo(this.projInfo.projectPath).subscribe(item => {
        if (item) {
          this.metadata.bizobjectID = item.bizobjectID;
          this.metadata.boCode = item.bizobjectCode;
        }
      });
    });

  }

  showModal(): void {
    this.showGuideWindow = true;
  }

  load(data: MetadataDto): any {
    this.showModal();
    HttpUrlConst.currentPath = data.relativePath;
  }

  createBe() {
    // webApi-CreateMetadata()
    //  
    //  be/

    // 
    this.gspMetadataService.InitializeMetadataEntity(this._metadataContnet).subscribe(response => {
      //  id
      const udtDto = this._metadataContnet;
      udtDto.id = (<MetadataDto>response).id;
      this.dependentInfo.type = this.udtType;
      this.dependentInfo.metadataAssembly = udtDto.nameSpace;
      this.dependentInfo.metadataId = udtDto.id;
      this.dependentInfo.metadataName = udtDto.name;
      this.dependentInfo.metadataCode = udtDto.code;
      this.initBeService.init(this.dependentInfo).subscribe(udtJson => {
        const udtMetadata = udtJson.json() as UdtMetadataContent;
        //  content=udt
        udtDto.content = JSON.stringify(udtMetadata);

        this.gspMetadataService.CreateMetadata(udtDto).subscribe(saveResponse => {
          this.createMetadataEvent.emit(udtDto);
          // 
          // tslint:disable-next-line:max-line-length
          // this.treeModel.addFileNode(this._metadataContnet.id, this._metadataContnet.fileName, '/' + this._metadataContnet.relativePath + '/' + this._metadataContnet.fileName);
          this.createMetadataSuccess.emit({
            action: '1',
            node: {
              nodeID: udtDto.fileName,
              nodeName: udtDto.fileName,
              nodePath: this.metadata.location + '/' + udtDto.fileName,
              nodeType: 'file'
            }
          });
          alert('!');
        }, error => {
          alert('!');
        });
      }, error => {
        alert(',!');
      });
    }, error => {
      alert(',!');
    });
  }

  /**
   * 
   */
  closeWindow() {
    this.showGuideWindow = false;
    this.closeModal.emit();
  }

  /**
   * 
   */
  commit() {
    this.closeWindow();
    this.createBe();
  }

  /**
   * 
   */
  cancel() {
    this.closeWindow();
  }

  /**
   * 
   */
  selectUdtType(newType: any) {
    this.udtType = newType;
  }
}

/**
 * 
 */
export class UdtGuideInfo {

  // udt://
  type: string;
  metadataId: string;
  metadataName: string;
  metadataCode: string;
  metadataAssembly: string;
}




