import { isNullOrUndefined } from 'util';
import { UdtObjectType, SimpleDataTypeDef, UdtElement, ComplexDataTypeDef } from './udt-reference';

export class UdtChangeIDElementLength {

  /**
   * id22=>36
   * 201810
   */
  static changeComplexUdtIDElementLength(udt: ComplexDataTypeDef) {
    if (udt.Elements && udt.Elements.length > 0) {
      udt.Elements.forEach(ele => {
        if (this.isIDType(ele)) {
          ele.Length = 36;
        }
      });
    }
  }

  /**
   * id22=>36
   * 201810
   */
  static changeSimpleUdtIDElementLength(udt: SimpleDataTypeDef) {
    if (this.isIDType(udt)) {
      udt.Length = 36;
    }
  }

  /**
   * id
   * id,parentId,
   * @param dataItem 
   */
  private static isIDType(dataItem: SimpleDataTypeDef | UdtElement) {
    if (dataItem.ObjectType === UdtObjectType.Association) {
      return true;
    }
    return false;
  }
}



