import { UnifiedDataTypeDef } from './UnifiedDataTypeDef';
import { BaseDataType } from './EnumType/BaseDataType';
import { UdtObjectType } from './EnumType/UdtObjectType';
import { UdtAssociation } from './Association/UdtAssociation';
import { UdtEnumValue } from './Enum/UdtEnumValue';

/**
 * 
 * @author wangjiegj<wangjiegj@inspur.com>
 */

/**
 * 
 */
export class SimpleDataTypeDef extends UnifiedDataTypeDef {

    /**
     * (string)
     */
    public DataType: BaseDataType = BaseDataType.String;

    /**
     * 
     */
    public Length: number;

    /**
     * 
     */
    public Precision: number;

    /**
     * 
     */
    public DefaultValue: string;

    /**
     * 
     */
    public IsUnique: boolean;

    /**
     * 
     */
    public IsRequired: boolean;

    /**
     * (None)
     */
    public ObjectType: UdtObjectType = UdtObjectType.None;

    /**
     * 
     */
    public ChildAssociations: UdtAssociation[];

    /**
     * 
     */
    public ContainEnumValues: UdtEnumValue[];

    // /**
    //  * 
    //  */
    // public IsMultiLanguage: boolean;

    /**
     * 
     */
    IsUdt: boolean;

    /**
     * 
     */
    UdtPkgName: string;

    /**
     * ID
     */
    UdtID: string;

    UnifiedDataType: UnifiedDataTypeDef;
}






