
import { Component, OnInit, Input, OnDestroy, OnChanges, SimpleChanges } from '@angular/core';
import { SelectableSettings } from '@progress/kendo-angular-grid';
import { isNullOrUndefined } from 'util';
import { Subscription } from 'rxjs/Subscription';
import {
  UdtAssociation, SimpleDataTypeDef,
  ViewAssociation, UdtElement, Guid, RefViewElementList
  , GSPBusinessEntity, IGSPCommonElement, GSPBizEntityElement, SimpleDataTypeConst
} from '../../../../service/udt-reference';
import { BizElementToUdtUtil } from '../../../../service/map-element-to-udt';

/**
 * 
 * ContainElements
 */
@Component({
  // tslint:disable-next-line:component-selector
  selector: 'association-refElement-gird',
  templateUrl: './association-refElement-grid.component.html',
  styleUrls: ['./association-refElement-grid.component.css']
})
export class AssociationRefElementGirdComponent implements OnInit, OnChanges, OnDestroy {

  /**
   * 
   */
  // @Input() private viewRefElements: refViewElementList;
  // @Input() private viewAsso: ViewAssociation;
  @Input() private currentAsso: UdtAssociation;

  /**
   * 
   */
  @Input() private refModel: GSPBusinessEntity;
  @Input() private currentElement: SimpleDataTypeDef;

  identifier = 'ID';

  /**
   * id
   */
  public eleSelection: string[] = [];

  /**
   * 
   */
  public refModelElements: IGSPCommonElement[];

  /**
   * 
   */
  public selectableSettings: SelectableSettings = {
    mode: 'multiple'
  };

  /**
   * 
   */
  refreshSubscription: Subscription;

  constructor() {
  }

  ngOnInit() {
    if (!isNullOrUndefined(this.refModel)) {
      this.load(this.currentAsso, this.refModel);
    }
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (changes['currentAsso']) {
      this.refresh(this.refModel);
    }
  }

  ngOnDestroy(): void {
    if (!isNullOrUndefined(this.refreshSubscription)) {
      this.refreshSubscription.unsubscribe();
    }
  }

  refresh(refModel: GSPBusinessEntity) {
    if (!isNullOrUndefined(refModel)) {
      this.refModel = refModel;
      this.load(this.currentAsso, this.refModel);
    } else {
      this.refModelElements = [];
    }
  }
  /**
   * 
   * @param refViewElements
   * @param refModel
   */
  load(asso: UdtAssociation, refModel: GSPBusinessEntity) {
    this.currentAsso = asso;
    this.getModelElements(refModel);
    this.setSelections(this.currentAsso);
    // this.refreshSubscription = this.viewRefElements.refreshView.subscribe(value => {
    //   this.getSelectElements(value);
    // });
  }

  /**
   * 
   * @param refModel
   */
  private getModelElements(refModel: GSPBusinessEntity): any {
    const mainObject = refModel.MainObject;
    this.refModelElements = mainObject.ContainElements;
  }

  private convertBeElementsToUdtElements(bizElements: IGSPCommonElement[]): UdtElement[] {
    const udtElements = new Array<UdtElement>();
    if (bizElements && bizElements.length > 0) {
      bizElements.forEach(ele => {
        const udtEle = new UdtElement();
        BizElementToUdtUtil.mapBizElementToUdtElement(<GSPBizEntityElement>ele, udtEle);
        udtElements.push(udtEle);
      });
    }
    return udtElements;
  }

  /**
   * 
   */
  private setSelections(asso: UdtAssociation): any {
    if (isNullOrUndefined(asso)) {
      return;
    }
    const refElements = asso.RefElementCollection;
    const selectionIds = new Array<string>();
    if (refElements && refElements.length > 0) {
      refElements.forEach(ele => {
        selectionIds.push(ele.RefElementId);
      });
    }
    this.eleSelection = selectionIds;
  }

  /**
   * 
   */
  private getSelectElements(): UdtElement[] {
    if (isNullOrUndefined(this.eleSelection) || isNullOrUndefined(this.refModelElements)) {
      return;
    }
    const refElements = new Array<UdtElement>();
    this.refModelElements.forEach(ele => {
      if (this.eleSelection.includes(ele.ID)) {
        const refEle = this.getRefElement(ele);
        refElements.push(refEle);
      }
    });
    return refElements;
  }

  getRefElement(ele: IGSPCommonElement): UdtElement {
    const newRefEle = new UdtElement();
    BizElementToUdtUtil.mapBizElementToUdtElement(<GSPBizEntityElement>ele, newRefEle);
    newRefEle.Id = Guid.newGuid().toString();
    newRefEle.LabelId = `${this.currentElement.Code}_${ele.LabelID}`;
    newRefEle.RefElementId = ele.ID;
    return newRefEle;
  }

  selectedKeysChange(args: any) {
    const result = this.getSelectElements();
    this.currentAsso.RefElementCollection = result;
  }
}



