/**
 * 
 */
enum UdtObjectType {
    /**
     * 
     */
    None = 'None',

    /**
     * 
     */
    Association = 'Association',

    /**
     * 
     */
    Enum = 'Enum',
}

export { UdtObjectType };

export const UdtObjectTypes: Array<{ text: string, value: UdtObjectType }> = [
    { text: '', value: UdtObjectType.None },
    { text: '', value: UdtObjectType.Association },
    { text: '', value: UdtObjectType.Enum }
];



