import { List } from 'immutable';
import { isNullOrUndefined } from 'util';
import { Subject } from 'rxjs/Subject';

import { ListBase } from '../base/listBase';
import { ViewAssociation } from './viewAssociation';
import { UdtAssociation } from '../../../UnifiedDataType';

export class ViewAssociationList extends ListBase<ViewAssociation> {

    public confirmAssociations: Subject<this> = new Subject<this>();

    constructor(assoArray: any) {
        super(assoArray);
        if (isNullOrUndefined(assoArray)) {
            return;
        } else {
            this.data = this.getViewAssociations(this.data);
        }
    }

    private getViewAssociations(assoArray: List<any>): any {
        if (isNullOrUndefined(assoArray)) {
            return;
        }

        let assoList = assoArray;

        if (assoArray.size > 0) {
            for (let i = 0; i < assoArray.size; i++) {
                assoList = assoList.update(i, () => this.getViewAssociation(assoArray.get(i)));
            }
            return assoList;
        }
    }

    private getViewAssociation(asso: UdtAssociation): any {
        return new ViewAssociation(asso);
    }
}






