import { UdtElement, BaseDataType, UdtObjectType } from './libs';
import {
    GSPBizEntityElement, GSPElementDataType, GSPElementObjectType
} from './udt-reference';

export class BizElementToUdtUtil {

    /**
     * beudtElement,asso.RefElementCollection
     * @param element be
     * @param udt 
     */
    static mapBizElementToUdtElement(element: GSPBizEntityElement, udt: UdtElement): any {
        udt.Code = element.Code;
        udt.Name = element.Name;
        udt.LabelId = element.LabelID;
        udt.Length = element.Length;
        udt.Precision = element.Precision;
        udt.DataType = this.convertUdtDataType(element.MDataType);
        udt.ObjectType = this.convertUdtObjectType(element.ObjectType);
        udt.DefaultValue = element.DefaultValue;
        udt.IsRequire = element.IsRequire;
    }


    /**
     * 
     * @param type
     */
    private static convertUdtDataType(type: GSPElementDataType): BaseDataType {
        switch (type) {
            case GSPElementDataType.Binary:
                return BaseDataType.Binary;
            case GSPElementDataType.Boolean:
                return BaseDataType.Boolean;
            case GSPElementDataType.Date:
                return BaseDataType.Date;
            case GSPElementDataType.DateTime:
                return BaseDataType.DateTime;
            case GSPElementDataType.Decimal:
                return BaseDataType.Decimal;
            case GSPElementDataType.Integer:
                return BaseDataType.Integer;
            case GSPElementDataType.String:
                return BaseDataType.String;
            case GSPElementDataType.Text:
                return BaseDataType.Text;
            default:
                alert('');
        }
    }

    /**
     * 
     * @param type
     */
    private static convertUdtObjectType(type: GSPElementObjectType): UdtObjectType {
        switch (type) {
            case GSPElementObjectType.None:
                return UdtObjectType.None;
            case GSPElementObjectType.Association:
                return UdtObjectType.Association;
            case GSPElementObjectType.Enum:
                return UdtObjectType.Enum;
            default:
                alert('');
        }
    }
}



