import { Map, List } from 'immutable';
import { MapBase } from '../base/mapBase';
import { isNullOrUndefined } from 'util';
import { ViewUseTypeInfoList } from './viewUseTypeInfoList';
import { ViewValidationInfoList } from '../viewValidationInfo/viewValidationInfoList';
import { UnifiedDataTypeDef } from '../../../UnifiedDataType/src/UnifiedDataTypeDef';
import { UnifiedDataTypeConst } from '../../../const';

export class ViewUnifiedDataTypeDef extends MapBase {

    /**
     * 
     * @param udt
     */
    constructor(udt: UnifiedDataTypeDef | Map<string, any>) {
        super(udt);
        if (isNullOrUndefined(udt)) {
            return;
        }
        this.data = this.getViewUseTypeInfo(this.data);
        this.data = this.getViewValiadtions(this.data);
    }

    private getViewUseTypeInfo(udt: any): any {
        const viewUdt = udt.update(
            UnifiedDataTypeConst.PropertyUseTypeInfos, infos => new ViewUseTypeInfoList(infos)
        );

        return viewUdt;
    }


    private getViewValiadtions(udt: any): any {
        const viewUdt = udt.update(
            UnifiedDataTypeConst.Validations, validations => new ViewValidationInfoList(validations)
        );

        return viewUdt;
    }

    set code(code: string) {
        this.set(UnifiedDataTypeConst.Code, code);
    }

    get code(): string {
        return this.get(UnifiedDataTypeConst.Code);
    }

    set name(name: string) {
        this.set(UnifiedDataTypeConst.Name, name);
    }

    get name(): string {
        return this.get(UnifiedDataTypeConst.Name);
    }

    set description(description: string) {
        this.set(UnifiedDataTypeConst.Description, description);
    }

    get description(): string {
        return this.get(UnifiedDataTypeConst.Description);
    }

    set propertyUseTypeInfos(infos: ViewUseTypeInfoList) {
        this.set(UnifiedDataTypeConst.PropertyUseTypeInfos, infos);
    }

    get propertyUseTypeInfos(): ViewUseTypeInfoList {
        return this.get(UnifiedDataTypeConst.PropertyUseTypeInfos);
    }

    get validations(): ViewValidationInfoList {
        const viewValidations = this.get(UnifiedDataTypeConst.Validations);
        if (isNullOrUndefined(viewValidations)) {
            this.set(UnifiedDataTypeConst.Validations, new ViewValidationInfoList(null));
        }
        return viewValidations;
    }

    set validations(validations: ViewValidationInfoList) {
        this.set(UnifiedDataTypeConst.Validations, validations);
    }

    get assemblyName(): string {
        return this.get(UnifiedDataTypeConst.AssemblyName);
    }

    set assemblyName(validations: string) {
        this.set(UnifiedDataTypeConst.AssemblyName, validations);
    }

}



