import { Component, OnInit, Input, OnChanges, SimpleChanges } from '@angular/core';
import { isNullOrUndefined } from 'util';
import { ViewSimpleDataTypeDef, UdtAssociation } from '../../../service/udt-reference';

@Component({
  selector: 'app-be-association-show-grid',
  templateUrl: './association-show-grid.component.html',
  styleUrls: ['./association-show-grid.component.css']
})
export class AssociationShowGridComponent implements OnInit, OnChanges {
  /**
   * 
   */
  @Input() currentViewElement: ViewSimpleDataTypeDef;
  assos: UdtAssociation[];

  constructor() { }

  ngOnInit() {
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (isNullOrUndefined(this.currentViewElement) || isNullOrUndefined(this.currentViewElement.associations)) {
      return;
    }
    this.assos = this.currentViewElement.associations.toJS();
  }

}



