import {
  ValidationInfo, Guid, ViewValidationInfo, ViewValidationInfoList, ViewSimpleDataTypeDef, ViewComplexDataTypeDef, ComplexDataTypeDef
} from '../../service/udt-reference';
import { Component, OnInit, OnChanges, SimpleChanges, Input, ViewChild } from '@angular/core';
import { isNullOrUndefined } from 'util';
import { BaseUdtOperationGridComponent } from './base-operation-grid/base-operation-grid.component';

@Component({
  selector: 'app-be-validations-tab',
  templateUrl: './validations-tab.component.html',
  styleUrls: ['./validations-tab.component.css']
})
export class UdtValidationsTabComponent implements OnInit, OnChanges {

  @ViewChild('grid') validationGrid: BaseUdtOperationGridComponent;

  /**
   * 
   */
  @Input() viewOperations: ViewValidationInfoList;

  /**
   * 
   */
  viewOperation: ViewValidationInfo;

  constructor() { }
  ngOnInit() {
    this.refresh();
  }

  ngOnChanges(changes: SimpleChanges): void {
    this.refresh();
  }

  load() {
    this.refresh();
  }

  protected refresh() {
    if (isNullOrUndefined(this.viewOperations) || isNullOrUndefined(this.viewOperations.toJS())) {
      this.viewOperation = null;
      return;
    }

    if (isNullOrUndefined(this.viewOperation) || !this.viewOperations.contains(this.viewOperation)) {
      // 
      this.viewOperations.setSelectIndex(0);
      this.viewOperation = this.viewOperations.currentSelectItem;
    }

    this.viewOperations.selectNewItem.subscribe(
      value => {
        this.viewOperation = this.viewOperations.currentSelectItem;
      }
    );
  }

  remove() {
    this.validationGrid.remove();
  }

  addValidation() {
    const newValidation = new ValidationInfo();
    newValidation.Id = Guid.newGuid();
    newValidation.Name = '';
    this.viewOperations.add(new ViewValidationInfo(newValidation));
    this.viewOperations.setSelectIndexByID(newValidation.Id);
    this.refresh(); // 
  }
}



