import {
  Component, ViewChild, Output, EventEmitter, TemplateRef, ComponentRef, ComponentFactoryResolver, Injector
} from '@angular/core';
import {
  ViewUdtElement, UdtObjectType
} from '../../../service/udt-reference';
// tslint:disable-next-line:max-line-length
import { ComplexObjectTypeEditorComponent } from '../../../complex-udt-designer/element-collection/complex-object-type-editor/complex-object-type-editor.component';
import { BsModalService } from '@farris/ui';

@Component({
  selector: 'app-complex-object-type-editor-in-udt',
  templateUrl: './object-type-editor-in-udt.component.html',
})
export class ComplexObjectTypeEditorInUdtComponent {

  isModalShown = false;

  viewElement: ViewUdtElement;

  @Output() changeObjType: EventEmitter<UdtObjectType> = new EventEmitter<UdtObjectType>();
  @ViewChild('buttons') btnRef: TemplateRef<any>;
  canEdit: boolean;
  compRef: ComponentRef<ComplexObjectTypeEditorComponent>;
  constructor(private modalService: BsModalService, private resolver: ComponentFactoryResolver, private injector: Injector) {
  }

  openObjectTypeEditor() {
    const compFactory = this.resolver.resolveComponentFactory(ComplexObjectTypeEditorComponent);
    const compRef = compFactory.create(this.injector);
    this.compRef = compRef;
    compRef.instance.viewElement = this.viewElement;
    compRef.instance.canEdit = this.canEdit;
    compRef.instance.changeObjType.subscribe($event => {
      this.changeObjType.next($event);
    });
    const modalConfig = {
      title: '',
      width: 710,
      height: 610,
      showButtons: true,
      buttons: this.btnRef
    };
    const dialog = this.modalService.show(compRef, modalConfig);
    compRef.instance.closeModalEmitter.subscribe(() => {
      dialog.close();
    });
  }

  onCancel() {
    this.compRef.instance.onCancel();
  }

  onConfirm() {
    this.compRef.instance.onConfirm();
  }
  /**
   * 
   */
  load(currentViewElement: ViewUdtElement, canEdit: boolean): any {
    this.viewElement = currentViewElement;
    this.canEdit = canEdit;
    this.openObjectTypeEditor();
  }
}





