import { Component, OnInit, Input, OnChanges, SimpleChanges } from '@angular/core';
import { ViewUseTypeInfoList, UseTypeInfo, ViewUseTypeInfo, UnifiedDataTypeConst, UseType } from '../../service/udt-reference';
import { Subscription } from 'rxjs/Subscription';
import { isNullOrUndefined } from 'util';

@Component({
  selector: 'app-select-property-use-type',
  templateUrl: './select-property-use-type.component.html',
  styleUrls: ['./select-property-use-type.component.css']
})
export class SelectPropertyUseTypeComponent implements OnInit, OnChanges {

  @Input() infos: ViewUseTypeInfoList;
  @Input() propertyName: string;

  /**
   * 
   */
  protected refreshSubscriptions = new Array<Subscription>();

  currentInfo: ViewUseTypeInfo;
  info: UseTypeInfo;
  canEdit: boolean;
  isConstraint: boolean;
  useTypeValue: UseType;

  useTypes = [
    { text: '', value: UseType.AsConstraint },
    { text: '', value: UseType.AsTemplate },
  ];

  constructor() { }

  ngOnInit() {
    this.refresh();
  }

  ngOnChanges(changes: SimpleChanges): void {
    this.refresh();
  }

  refresh() {
    if (isNullOrUndefined(this.infos)) {
      this.clear();
      return;
    }
    this.currentInfo = this.infos.getItemByPropertyName(this.propertyName);
    if (isNullOrUndefined(this.currentInfo)) {
      // alert('');
      console.log(this.propertyName);
      console.log(this.infos);
      this.clear();
      return;
    }
    this.info = this.currentInfo.toJS();
    this.canEdit = this.info.CanEdit;
    this.isConstraint = this.info.PropertyUseType === UseType.AsConstraint;
    this.useTypeValue = this.info.PropertyUseType;
  }

  clear() {
    this.info = new UseTypeInfo();
    this.info.PropertyUseType = UseType.AsTemplate;
    this.canEdit = false;
    this.isConstraint = false;
  }

  selectStateChange(newValue: UseType) {
    this.currentInfo.set(UnifiedDataTypeConst.PropertyUseType, newValue);
  }
}



