import { List, Map } from 'immutable';
import { isNullOrUndefined } from 'util';
import { ListBase } from '../base/listBase';
import { MapBase } from '../base/mapBase';
import { UdtEnumValue } from '../../../UnifiedDataType';

export class ViewEnumValueList extends ListBase<ViewEnumValue> {

    constructor(enumArray: UdtEnumValue[] | List<any>) {
        super(enumArray);
        if (isNullOrUndefined(enumArray)) {
            return;
        }
        this.data = this.getViewEnumValues(this.data);
    }

    getViewEnumValues(enumArray: List<any>): any {
        if (isNullOrUndefined(enumArray)) {
            return;
        }

        let viewEnumValueList = enumArray;

        if (enumArray.size > 0) {
            for (let i = 0; i < enumArray.size; i++) {
                viewEnumValueList = viewEnumValueList.update(i, () => this.getViewEnumValue(enumArray.get(i)));
            }
            return viewEnumValueList;
        }
    }

    getViewEnumValue(enumValue: UdtEnumValue): any {
        return new ViewEnumValue(enumValue);
    }
}

export class ViewEnumValue extends MapBase {

    /**
     * 
     * @param element
     */
    constructor(enumValue: UdtEnumValue | Map<string, any>) {
        super(enumValue);
    }
}




