import { Validators } from '@angular/forms';
import { isNullOrUndefined } from 'util';
import { Component, Input, ViewChild } from '@angular/core';
import {
  ViewSimpleDataTypeDef, SimpleDataTypeDef, BaseDataType,
  UdtObjectType, BaseDataTypes, SimpleDataTypeConst, UdtObjectTypes, ViewUseTypeInfoList, GSPEnumValue, UdtAssociation
} from '../../service/udt-reference';
import { EnumEditComponent } from './enum-edit/enum-edit.component';
import { AssociationEditComponent } from './association-edit/association-edit.component';
import { ObjectTypeEditorComponent } from './object-type-editor/object-type-editor.component';
import { BaseCardComponent } from '../../basic-info-tab/base-form';
// tslint:disable-next-line:max-line-length
import { ObjectTypeEditorInUdtComponent } from '../../ref-modules/object-type-editor/object-type-editor/object-type-editor-in-udt.component';

@Component({
  selector: 'app-property-settings-tab',
  templateUrl: './property-settings-tab.component.html',
  styleUrls: ['./property-settings-tab.component.css']
})
export class PropertySettingsTabComponent extends BaseCardComponent {

  @ViewChild('enumsComponent') enumsComponent: EnumEditComponent;
  @ViewChild('associationComponent') associationComponent: AssociationEditComponent;
  @ViewChild('objectTypeEditor') objectTypeEditor: ObjectTypeEditorInUdtComponent;

  @Input() currentViewModel: ViewSimpleDataTypeDef;

  get infos(): ViewUseTypeInfoList {
    if (isNullOrUndefined(this.currentViewModel)) {
      return null;
    }
    const infos = this.currentViewModel.propertyUseTypeInfos;
    return infos;
  }

  get isNoneObjectType(): boolean {
    if (isNullOrUndefined(this.data)) {
      return true;
    }
    return this.data.ObjectType === UdtObjectType.None;
  }

  data: SimpleDataTypeDef;

  dataTypes = BaseDataTypes;
  objectTypes = UdtObjectTypes;

  get assoOrEnum(): string {
    const enumString = '';
    const assoString = '';
    return this.isEnum ? enumString : assoString;
  }

  get isEnum(): boolean {
    if (isNullOrUndefined(this.data)) {
      return false;
    }
    if (this.data.ObjectType === UdtObjectType.Enum) {
      return true;
    }
    return false;
  }

  /**
   * -[][][]
   * []-canEdit=false;
   * [][]
   */
  get assoOrEnumPropertyName(): string {
    const enumString = SimpleDataTypeConst.ContainEnumValues;
    const assoString = SimpleDataTypeConst.ChildAssociations;
    return this.isNoneObjectType ? enumString : (this.isEnum ? enumString : assoString);
  }

  get assoOrEnumValue(): string {
    if (isNullOrUndefined(this.data)) {
      return '';
    }
    return this.isEnum ? this.transformEnumValues(this.data.ContainEnumValues) : this.transformAssociations(this.data.ChildAssociations);
  }

  /**
   * 
   * @param enumList 
   */
  transformEnumValues(enumList: GSPEnumValue[]): string {
    if (enumList == null || enumList.length === 0) {
      return '';
    }
    let enums = '';
    enumList.forEach(enumItem => {
      enums += enumItem.Value + ':' + enumItem.Name + ',';
    });
    return enums.slice(0, enums.length - 1);
  }

  /**
   * 
   * @param childAssociations 
   */
  transformAssociations(childAssociations: UdtAssociation[]): string {
    if (childAssociations == null || childAssociations.length === 0) {
      return '';
    }
    let models = '';
    childAssociations.forEach(ass => {
      models += ass.RefEntityName + ',';
    });
    return models.slice(0, models.length - 1);
  }

  get canChangeDataType(): boolean {
    if (isNullOrUndefined(this.data)) {
      return false;
    }
    if (this.data.ObjectType === UdtObjectType.Association) {
      return false;
    }
    return true;
  }

  get canChangeLength(): boolean {
    if (isNullOrUndefined(this.data)) {
      return false;
    }
    if (this.data.ObjectType === UdtObjectType.Association) {
      return false;
    }
    const info = this.dataTypes.find(item => item.value === this.data.DataType);
    if (!isNullOrUndefined(info)) {
      return info.canChangeLength;
    }
    return false;
  }

  get canChangePrecision(): boolean {
    if (isNullOrUndefined(this.data)) {
      return false;
    }
    if (this.data.ObjectType === UdtObjectType.Association) {
      return false;
    }
    const info = this.dataTypes.find(item => item.value === this.data.DataType);
    if (!isNullOrUndefined(info)) {
      return info.canChangePresicion;
    }
    return false;
  }

  /**
   * 
   */
  protected createForm() {
    this.formGroup = this.formBuilder.group({
      dataType: '',
      precision: ['', Validators.compose([Validators.required, Validators.pattern('^[0-9]{1,3}')])],
      objectType: '',
      isUnique: false,
      length: ['', Validators.compose([Validators.required, Validators.pattern('^[0-9]{1,4}')])],
      defaultValue: '',
      assoOrEnums: '',
      isRequired: false
    });
  }

  /**
   * 
   * @param udt
   */
  protected setFormValue(udt: SimpleDataTypeDef) {
    this.unsubscribeFormGroupValueChanges();
    if (isNullOrUndefined(udt)) {
      this.clearFormValue();
    }
    if (isNullOrUndefined(this.formGroup)) {
      this.createForm();
    }
    this.formGroup.patchValue({
      dataType: udt.DataType || '',
      precision: isNullOrUndefined(udt.Precision) ? '' : udt.Precision,
      objectType: udt.ObjectType || '',
      isUnique: udt.IsUnique || '',
      // isMultiLanguage: udt.IsMultiLanguage || '',

      length: isNullOrUndefined(udt.Length) ? '' : udt.Length,
      defaultValue: udt.DefaultValue || '',
      assoOrEnums: this.assoOrEnumValue || '',
      isRequired: udt.IsRequired || ''
    });
    // 
    this.formGroup.enable();
    if (!this.canChangeLength) {
      this.formGroup.controls.length.disable();
    }
    if (!this.canChangePrecision) {
      this.formGroup.controls.precision.disable();
    }
    if (!this.canChangeDataType) {
      this.formGroup.controls.dataType.disable();
    }
    if (this.isNoneObjectType) {
      this.formGroup.controls.assoOrEnums.disable();
    }
    this.subscribeFormGroupValueChanges();
  }

  /**
   * 
   */
  protected clearFormValue() {
    this.unsubscribeFormGroupValueChanges();
    if (isNullOrUndefined(this.formGroup)) {
      this.createForm();
    }
    this.formGroup.disable();
    this.formGroup.patchValue({
      dataType: '',
      precision: '',
      objectType: '',
      isUnique: false,
      length: '',
      defaultValue: '',
      assoOrEnums: '',
      isRequired: false
    });
    this.subscribeFormGroupValueChanges();
  }

  /**
   * 
   */
  protected subscribeFormGroupValueChanges() {
    const subscriptionDataType = this.formGroup.controls.dataType.valueChanges.subscribe(datatype => {
      if (datatype !== this.data.DataType) {
        this.currentViewModel.set(SimpleDataTypeConst.DataType, datatype);
        this.changeLengthPrecisionByType(datatype);
      }
    });
    const subscriptionLength = this.formGroup.controls.length.valueChanges.subscribe(
      length => {
        this.currentViewModel.set(SimpleDataTypeConst.Length, length);
      });
    const subscriptionPrecision = this.formGroup.controls.precision.valueChanges.subscribe(precision =>
      this.currentViewModel.set(SimpleDataTypeConst.Precision, precision));

    const subscriptionIsUnique = this.formGroup.controls.isUnique.valueChanges.subscribe(isUnique => {
      this.currentViewModel.set(SimpleDataTypeConst.IsUnique, isUnique);
    });
    const subscriptionIsRequired = this.formGroup.controls.isRequired.valueChanges.subscribe(isRequired => {
      this.currentViewModel.set(SimpleDataTypeConst.IsRequired, isRequired);
    });
    const subscriptionObjectType = this.formGroup.controls.objectType.valueChanges.subscribe(
      objectType => {
        this.currentViewModel.set(SimpleDataTypeConst.ObjectType, objectType);
        this.changeObjectType(objectType);
      });
    const subscriptionDefaultValue = this.formGroup.controls.defaultValue.valueChanges.subscribe(defaultValue =>
      this.currentViewModel.set(SimpleDataTypeConst.DefaultValue, defaultValue));

    if (isNullOrUndefined(this.formGroupSubscriptions)) {
      this.formGroupSubscriptions = new Array<any>();
    }
    this.formGroupSubscriptions.push(subscriptionDataType);
    this.formGroupSubscriptions.push(subscriptionLength);
    this.formGroupSubscriptions.push(subscriptionPrecision);
    this.formGroupSubscriptions.push(subscriptionIsUnique);
    this.formGroupSubscriptions.push(subscriptionIsRequired);
    this.formGroupSubscriptions.push(subscriptionObjectType);
    this.formGroupSubscriptions.push(subscriptionDefaultValue);
  }

  /**
   * /
   */
  changeObjectType(newValue: UdtObjectType) {
    switch (newValue) {
      // 360
      case UdtObjectType.Association:
        this.currentViewModel.set(SimpleDataTypeConst.Length, 36);
        this.currentViewModel.set(SimpleDataTypeConst.Precision, 0);
        this.currentViewModel.set(SimpleDataTypeConst.DataType, BaseDataType.String);
        this.currentViewModel.clearEnumValues();
        break;
      case UdtObjectType.Enum:
        this.currentViewModel.clearAssociations();
        break;
      case UdtObjectType.None:
        this.currentViewModel.clearAssociations();
        this.currentViewModel.clearEnumValues();
        break;
      default:
        alert('' + newValue);
    }
    if (newValue !== UdtObjectType.Association) {
      this.currentViewModel.clearAssociations();
    }
    if (newValue !== UdtObjectType.Enum) {
      this.currentViewModel.clearEnumValues();
    }
  }

  /**
   * 
   */
  changeLengthPrecisionByType(type: BaseDataType) {
    const length = this.dataTypes.find(item => item.value === type).length;
    const precision = this.dataTypes.find(item => item.value === type).precision;
    this.currentViewModel.set(SimpleDataTypeConst.Length, length);
    this.currentViewModel.set(SimpleDataTypeConst.Precision, precision);
  }


  /**
   * 
   */
  editEnumValue() {
    // this.enumsComponent.load((this.currentViewModel));
    this.objectTypeEditor.load(this.currentViewModel, this.isEnum);
  }

  /**
   * 
   */
  editAsso() {
    // this.associationComponent.load(this.currentViewModel);
    this.objectTypeEditor.load(this.currentViewModel, this.isEnum);
  }

  editAssoOrEnum() {
    if (this.isNoneObjectType) {
      return;
    }
    if (this.isEnum) {
      this.editEnumValue();
    } else {
      this.editAsso();
    }
  }
}




