import { Map } from 'immutable';
import { MapBase } from '../base/mapBase';
import { UdtAssociationKey } from '../../../UnifiedDataType';
import { SimpleDataTypeConst } from '../../../const';

export class ViewAssoKey extends MapBase {

    /**
     * 
     * @param element
     */
    constructor(asso: UdtAssociationKey | Map<string, any>) {
        super(asso);
    }

    updateSourceElement(id: string, name: string) {
        this.set(SimpleDataTypeConst.SourceElement, id);
        this.set(SimpleDataTypeConst.SourceElementDisplay, name);
    }

    updateTargetElement(id: string, name: string) {
        this.set(SimpleDataTypeConst.TargetElement, id);
        this.set(SimpleDataTypeConst.TargetElementDisplay, name);
    }
}



