import { Component, OnInit, ViewChild, EventEmitter, Output } from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { isNullOrUndefined } from 'util';
import { SelectableSettings } from '@progress/kendo-angular-grid';
import { EnumEditComponent } from '../enum-edit/enum-edit.component';
import { Subscription } from 'rxjs/Subscription';
import { AssociationEditComponent } from '../association-edit/association-edit.component';
import {
  SimpleDataTypeDef, ViewSimpleDataTypeDef, GSPAssociation, UdtObjectType
} from '../../../service/udt-reference';

@Component({
  selector: 'app-object-type-editor',
  templateUrl: './object-type-editor.component.html',
  styleUrls: ['./object-type-editor.component.css']
})
export class ObjectTypeEditorComponent implements OnInit {

  @Output() closeModalEmitter: EventEmitter<any> = new EventEmitter<any>();

  public formGroup: FormGroup;

  isEnum: boolean;

  get currentElement(): SimpleDataTypeDef {
    if (isNullOrUndefined(this.viewElement)) {
      return null;
    }
    return this.viewElement.toJS();
  }

  get enumEditorOrAssoEditor(): string {
    return this.isEnum ? '' : '';
  }

  @ViewChild('associationComponent') associationComponent: AssociationEditComponent;
  @ViewChild('enumsComponent') enumsComponent: EnumEditComponent;

  IsObjectTypeNone = false;
  IsObjectTypeAsso = false;
  IsObjectTypeEnum = false;

  /**
   * 
   * Subscription
   */
  private formGroupSubscriptions: any[] = new Array<Subscription>();

  isNew = false;
  active = false;

  viewElement: ViewSimpleDataTypeDef;
  // element: SimpleDataTypeDef;
  assoValues: GSPAssociation[];
  tempAssos: GSPAssociation[];
  public seletedKeys: any[] = [];
  public selectableSettings: SelectableSettings = {
    checkboxOnly: false,
    mode: 'single'
  };

  get canAdd(): boolean {
    if (isNullOrUndefined(this.assoValues)) {
      return true;
    }
    return this.assoValues.length < 1;
  }

  ngOnInit(): void {
    this.load(this.viewElement, this.isEnum);
  }

  constructor(private formBuilder: FormBuilder) {
  }

  load(viewEle: ViewSimpleDataTypeDef, isEnum: boolean) {
    this.isEnum = isEnum;
    this.active = true;
    this.viewElement = viewEle;
  }

  /**
   * 
   */
  getCurrentObjecttTypeEditor() {
    switch (this.viewElement.objectType) {
      case UdtObjectType.Enum:
        return this.enumsComponent;
      case UdtObjectType.Association:
        return this.associationComponent;
    }
  }

  /**
   * 
   * @param e
   */
  public onConfirm(): void {
    const childEditor = this.getCurrentObjecttTypeEditor();
    if (!isNullOrUndefined(childEditor)) {
      // 
      if (!childEditor.checkBeforeSave()) {
        return;
      } else {
        // 
        childEditor.onConfirm();
      }
    }

    // 
    const type = this.viewElement.objectType;
    if (type !== UdtObjectType.Association) {
      this.viewElement.clearAssociations();
    }
    if (type !== UdtObjectType.Enum) {
      this.viewElement.clearEnumValues();
    }

    this.close();
  }

  /**
   * 
   */
  public onCancel(): void {
    this.close();
  }

  /**
   * 
   */
  private closeForm(): void {
    this.close();
  }

  private close() {
    this.active = false;
    this.closeModalEmitter.next();
  }
}




