import { List, Map, fromJS } from 'immutable';
import { isNullOrUndefined } from 'util';
import { Subject } from 'rxjs/Subject';
import { MapBase } from '../base/mapBase';
import { ViewElementList } from '../viewDataTypeDef/viewSimpleDataTypeDef';
import { ViewUnifiedDataTypeDef } from '../viewDataTypeDef/viewUnifiedDataTypeDef';
import { ComplexDataTypeDef, UdtDbInfo } from '../../../UnifiedDataType';
import { ComplexDataTypeConst } from '../../../const';
import { ViewUdtElementList } from './viewUdtElement';
import { ViewUdtDbInfo } from './viewUdtDbInfo';
import { ViewDeterminationList } from '../viewDetermination/viewDetermination';

export class ViewComplexDataTypeDef extends ViewUnifiedDataTypeDef {

  /**
   * 
   */
  refreshName: Subject<boolean> = new Subject<boolean>();

  /**
   * 
   * @param object
   */
  constructor(object: ComplexDataTypeDef | Map<string, any>) {
    super(object);
    if (isNullOrUndefined(object)) {
      return;
    }
    this.data = this.getCurrentViewObject(this.data);
  }

  /**
   * Map
   * @param bizObject
   */
  private getCurrentViewObject(bizObject: Map<string, any>): Map<string, any> {
    if (isNullOrUndefined(bizObject)) {
      return;
    }
    let vo = bizObject;
    vo = vo.update(ComplexDataTypeConst.DbInfo, dbInfo => this.getUdtDbInfo(dbInfo));
    vo = vo.update(ComplexDataTypeConst.Elements, elements => this.getElementViewList(elements));
    vo = vo.update(ComplexDataTypeConst.DtmBeforeSave, dtms => this.getDtmBeforeSave(dtms));
    vo = vo.update(ComplexDataTypeConst.DtmAfterModify, dtms => this.getDtmAfterModify(dtms));
    vo = vo.update(ComplexDataTypeConst.DtmAfterCreate, dtms => this.getDtmAfterCreate(dtms));
    return vo;
  }

  private getUdtDbInfo(info: UdtDbInfo): any {
    return new ViewUdtDbInfo(info);
  }

  private getElementViewList(bizElements: List<any>): ViewUdtElementList {
    const elementList = new ViewUdtElementList(bizElements);
    return elementList;
  }

  private getDtmBeforeSave(dtms: List<any>): ViewDeterminationList {
    const elementList = new ViewDeterminationList(dtms);
    return elementList;
  }

  private getDtmAfterModify(dtms: List<any>): ViewDeterminationList {
    const elementList = new ViewDeterminationList(dtms);
    return elementList;
  }

  private getDtmAfterCreate(dtms: List<any>): ViewDeterminationList {
    const elementList = new ViewDeterminationList(dtms);
    return elementList;
  }

  get elements(): ViewUdtElementList {
    return this.get(ComplexDataTypeConst.Elements);
  }

  set elements(elements: ViewUdtElementList) {
    this.set(ComplexDataTypeConst.Elements, elements);
  }

  get DtmBeforeSave(): ViewDeterminationList {
    const viewDeterminations = this.get(ComplexDataTypeConst.DtmBeforeSave);
    if (isNullOrUndefined(viewDeterminations)) {
      this.set(ComplexDataTypeConst.DtmBeforeSave, new ViewDeterminationList(null));
    }
    return viewDeterminations;
  }

  get DtmAfterModify(): ViewDeterminationList {
    const viewDeterminations = this.get(ComplexDataTypeConst.DtmAfterModify);
    if (isNullOrUndefined(viewDeterminations)) {
      this.set(ComplexDataTypeConst.DtmAfterModify, new ViewDeterminationList(null));
    }
    return viewDeterminations;
  }

  get DtmAfterCreate(): ViewDeterminationList {
    const viewDeterminations = this.get(ComplexDataTypeConst.DtmAfterCreate);
    if (isNullOrUndefined(viewDeterminations)) {
      this.set(ComplexDataTypeConst.DtmAfterCreate, new ViewDeterminationList(null));
    }
    return viewDeterminations;
  }
}



