import {
  ComponentRef, Component, ViewChild, Input, OnChanges, Output, EventEmitter,
  SimpleChanges, ComponentFactoryResolver, Injector, TemplateRef
} from '@angular/core';
import { isNullOrUndefined } from 'util';
import { BsModalService, BsModalRef } from '@farris/ui';
// tslint:disable-next-line:max-line-length
import { ObjectTypeEditorComponent } from '../../../simple-udt-designer/property-settings-tab/object-type-editor/object-type-editor.component';
import { ViewSimpleDataTypeDef, UdtObjectType } from '../../../service/udt-reference';

@Component({
  selector: 'app-object-type-editor-in-udt',
  templateUrl: './object-type-editor-in-udt.component.html',
})
export class ObjectTypeEditorInUdtComponent {

  isModalShown = false;

  @Input() viewElement: ViewSimpleDataTypeDef;
  @Input() isEnum: boolean;

  @ViewChild('buttons') btnRef: TemplateRef<any>;
  canEdit: boolean;
  compRef: ComponentRef<ObjectTypeEditorComponent>;
  constructor(private modalService: BsModalService, private resolver: ComponentFactoryResolver, private injector: Injector) {
  }

  openObjectTypeEditor() {
    const compFactory = this.resolver.resolveComponentFactory(ObjectTypeEditorComponent);
    const compRef = compFactory.create(this.injector);
    this.compRef = compRef;
    compRef.instance.viewElement = this.viewElement;
    compRef.instance.isEnum = this.isEnum;
    const modalConfig = {
      title: compRef.instance.enumEditorOrAssoEditor,
      width: 710,
      height: 610,
      showButtons: true,
      buttons: this.btnRef
    };
    const dialog = this.modalService.show(compRef, modalConfig);
    compRef.instance.closeModalEmitter.subscribe(() => {
      dialog.close();
    });
  }

  onCancel() {
    this.compRef.instance.onCancel();
  }

  onConfirm() {
    this.compRef.instance.onConfirm();
  }
  /**
   * 
   */
  load(currentViewElement: ViewSimpleDataTypeDef, isEnum: boolean): any {
    this.viewElement = currentViewElement;
    this.isEnum = isEnum;
    this.openObjectTypeEditor();
  }
}



