import { Component, OnInit, Input, EventEmitter, Output, OnChanges, SimpleChanges } from '@angular/core';
import { UdtElement, ComplexDataTypeDef, RequestChildElement, ComplexDataTypeConst } from '../../../../service/udt-reference';
import { SelectableSettings } from '@progress/kendo-angular-grid';
import { Observable } from 'rxjs/Observable';
import { of } from 'rxjs/observable/of';

@Component({
  selector: 'app-trigger-elements-grid',
  templateUrl: './trigger-elements-grid.component.html',
  styleUrls: ['./trigger-elements-grid.component.css']
})
export class TriggerElementsGridComponent implements OnInit, OnChanges {

  /**
   * 
   */
  @Input() triggerElements: UdtElement[];
  @Input() currentObject: ComplexDataTypeDef;

  elements: UdtElement[];
  bizObjects: any[];
  active = false;
  expandedKeys: string[] = [];
  selectedKeys: string[] = [];
  identifier = ComplexDataTypeConst.IDENTIFIER;

  @Output() cancel: EventEmitter<any> = new EventEmitter();
  @Output() save: EventEmitter<RequestChildElement> = new EventEmitter();
  /**
   * 
   */
  private selectableSettings: SelectableSettings = {
    mode: 'multiple'
  };

  /**
   * id
   */
  private eleSelection: string[] = [];

  constructor() { }

  ngOnInit() {

  }

  ngOnChanges(changes: SimpleChanges): void {

  }

  /**
   * 
   * @param refViewElements 
   * @param refModel 
   */
  load(triggerElements: UdtElement[], currentObject: ComplexDataTypeDef) {
    this.active = true;
    this.triggerElements = triggerElements;
    this.currentObject = currentObject;
    this.getObjectElements(currentObject);
    this.getSelectElements(triggerElements);
  }

  /**
   * 
   * @param refModel 
   */
  private getObjectElements(currentObject: ComplexDataTypeDef): any {
    this.elements = currentObject.Elements;
  }

  /**
   * 
   */
  private getSelectElements(triggerElements: UdtElement[]): any {
    const selectionIds = new Array<string>();
    if (triggerElements && triggerElements.length > 0) {
      triggerElements.forEach(ele => {
        selectionIds.push(ele.Id);
      });
    }
    this.eleSelection = selectionIds;
  }


  public onConfirm(e): void {
    e.preventDefault();
    const requestChildElement = new RequestChildElement();
    if (this.eleSelection.length === 0) {
      this.save.emit(null);
    } else {
      requestChildElement.RequestChildElementValue = this.eleSelection;
      this.save.emit(requestChildElement);
    }
    this.closeForm();
  }

  public onCancel(e): void {
    e.preventDefault();
    this.closeForm();
  }

  private closeForm(): void {
    this.active = false;
  }

  /**
   * 
   * @param node
   */
  public fetchChildren(node: any): Observable<any[]> {
    // nodeBE
    if (node.MainObject) {
      return of([node.MainObject]); // BEMainObject
    } else {
      return of(node.ContainChildObjects); // 
    }
  }

  /**
   * 
   * @param node
   */
  public hasChildren(node: any): boolean {
    // BE
    if (node.MainObject) {
      return true;
    } else {
      return node.ContainChildObjects && node.ContainChildObjects.length > 0;
    }
  }
}



