export class BeOperationTypeMapComponentType {
    public static findComponentTypeByBeOperationType(opType: string): string {
        switch (opType) {
            case 'BizMgrAction':
                return 'BEMgrComponent';
            case 'BizAction':
                return 'BEComponent';
            case 'Validation':
                return 'ValidationComponent';
            case 'Determination':
                return 'DeterminationComponent';
            default:
                throw new Error(`${opType}`);
        }
    }
}



