import { PackageView } from '@farris/ide-devkit';
import { ComponentFactoryResolver, Injector } from '@angular/core';
import { UdtDesingerFrameComponent } from './udt-designer-frame/udt-desinger-frame.component';
import { UdtDesignerFrameModule } from './udt-designer-frame/udt-designer-frame.module';
import { UdtDesignerGuideComponent } from './udt-designer-frame/ref-modules/udt-designer-guide/udt-designer-guide.component';
export class UdtDesignerPackageView extends PackageView {

  constructor(serializedState: any,
    private cfr: ComponentFactoryResolver,
    private injector: Injector) {
    super(serializedState);
    const componentFactory = this.cfr.resolveComponentFactory(UdtDesingerFrameComponent);
    const moduleRef = this.injector.get(UdtDesignerFrameModule) as UdtDesignerFrameModule;
    console.log(moduleRef);
    this.element = componentFactory.create(this.injector);
  }
}

export class UdtDesignerGuidePackageView extends PackageView {

  constructor(serializedState: any,
    private cfr: ComponentFactoryResolver,
    private injector: Injector) {
    super(serializedState);
    const componentFactory = this.cfr.resolveComponentFactory(UdtDesignerGuideComponent);
    const moduleRef = this.injector.get(UdtDesignerFrameModule) as UdtDesignerFrameModule;
    console.log(moduleRef);
    this.element = componentFactory.create(this.injector);
  }
}



