import { Injectable } from '@angular/core';
import { Headers, RequestOptions, Http } from '@angular/http';
import { Observable } from 'rxjs/Observable';
import { CacheService } from '@farris/ide-devkit/services';
import { GSPMetadataService, MetadataDto } from '@gsp-lcm/metadata-selector';
import { isNullOrUndefined } from 'util';
import { HttpUrlConst, GSPBusinessEntity } from './udt-reference';
import { Subject } from 'rxjs/Subject';

/**
 * udt
 */
@Injectable()
export class UdtMetadataService {

  /**
   * 
   */
  // getRefModel = new Subject<GSPBusinessEntity>();

  constructor(private cache: CacheService, private lcmService: GSPMetadataService, ) {
  }
  private sessionId = this.cache.get('sessionId');

  /**
   * be,sessionId
   */
  saveUdtMetadata(_metadataContnet: MetadataDto): Observable<any> {
    return this.lcmService.SaveMetadata(_metadataContnet, this.sessionId);
  }

  /**
   * be
   */
  loadUdtMetadata(fileName: string, path: string): Observable<any> {
    return this.lcmService.LoadMetadata(fileName, path);
  }

  // /**
  //  * vmbe
  //  */
  // getRefBeMetadata(pkgName: string, metadataId: string) {
  //   if (isNullOrUndefined(metadataId) || metadataId.length === 0) {
  //     alert('ID');
  //     return;
  //   }
  //   const path = HttpUrlConst.currentPath;
  //   this.lcmService.GetRefMetadata(path, metadataId, this.sessionId).subscribe(data => {
  //     const meta = data as MetadataDto;
  //     const be = JSON.parse(meta.content) as GSPBusinessEntity;
  //     this.getRefModel.next(be);
  //   }, error => alert(''));
  // }

  /**
   * udt
   */
  GetRefMetadata(path: string, metadataId: string) {
    if (isNullOrUndefined(metadataId) || metadataId.length === 0) {
      alert('ID');
      return;
    }
    return this.lcmService.GetRefMetadata(path, metadataId, this.sessionId);
  }
}



