import { SimpleDataTypeDef, BaseDataType, UdtObjectType, UdtAssociation } from './udt-reference';
import { isNullOrUndefined, isDate, isBoolean } from 'util';

export class ValidateBeforeSave {
    static ValidateSimpleDataType(udt: SimpleDataTypeDef): boolean {
        if (!this.ValidateElementBasicInfo(udt)) {
            return false;
        }

        if (!this.ValidateElementDefaultValue(udt)) {
            return false;
        }
        return true;
    }

    /**
     * 
     * @param udt 
     */
    private static ValidateElementDefaultValue(udt: SimpleDataTypeDef): boolean {
        // todo:
        const defaultValue = udt.DefaultValue;
        const type = udt.DataType;
        const length = udt.Length;
        const reInt = new RegExp('^-?\\d+$');
        const reDecimal = new RegExp('^(([0-9]+\\.[0-9]*)|([0-9]*\\.[0-9]+)|([1-9][0-9]+)|([0-9]))$');
        if (this.isNullOrEmptyString(defaultValue)) {
            return true;
        }
        let result = true;
        switch (type) {
            case BaseDataType.String:
            case BaseDataType.Text:
                break;
            case BaseDataType.Integer:
                result = reInt.test(defaultValue);
                break;
            case BaseDataType.Decimal:
                result = reDecimal.test(defaultValue);
                break;
            case BaseDataType.Date:
            case BaseDataType.DateTime:
                // result = isDate(defaultValue);
                break;
            case BaseDataType.Boolean:
                // result = isBoolean(defaultValue);
                result = defaultValue === 'true' || defaultValue === 'false' || defaultValue === '' || defaultValue === ''
                    || defaultValue === '0' || defaultValue === '1';
                break;
            case BaseDataType.Binary:
                result = defaultValue.trim() === '';
                break;
        }
        if (!result) {
            alert('[][]');
        }
        return result;
    }

    /**
     * 
     * @param udt 
     */
    private static ValidateElementBasicInfo(udt: SimpleDataTypeDef): boolean {
        if (this.isNullOrEmptyString(udt.Code)) {
            alert('');
            return false;
        }
        if (this.isNullOrEmptyString(udt.Name)) {
            alert('');
            return false;
        }
        if (udt.ObjectType === UdtObjectType.Association) {
            if (!this.ValidateIDElementTypeAndLength(udt)) {
                alert(' [] , ID[], []36');
                return false;
            }
            if (!this.validateAssos(udt.ChildAssociations)) {
                return false;
            }
            udt.ChildAssociations.forEach(asso => {
                this.validateAsso(asso);
            });
        } else if (udt.ObjectType === UdtObjectType.Enum) {
            if (this.isNullOrEmptyList(udt.ContainEnumValues)) {
                alert('[] , [] ');
                return false;
            }
            for (let i = 0; i < udt.ContainEnumValues.length; i++) {
                const enumValue = udt.ContainEnumValues[i];
                if (this.isNullOrEmptyString(enumValue.Name) || this.isNullOrEmptyString(enumValue.Value)) {
                    alert('');
                    return false;
                }
            }
        }
        return true;
    }

    static validateAssos(assos: UdtAssociation[]): boolean {
        if (isNullOrUndefined(assos) || assos.length === 0) {
            alert(' [] , [] !');
            return false;
        }
        for (let i = 0; i < assos.length; i++) {
            if (!this.validateAsso(assos[i])) {
                return false;
            }
        }
        return true;
    }

    static validateAsso(asso: UdtAssociation): boolean {
        if (isNullOrUndefined(asso) || this.isNullOrEmptyList(asso.KeyCollection)) {
            alert(' [] , [] !');
            return false;
        }
        for (let i = 0; i < asso.KeyCollection.length; i++) {
            const key = asso.KeyCollection[i];
            if (isNullOrUndefined(key) || this.isNullOrEmptyString(key.SourceElement) || this.isNullOrEmptyString(key.TargetElement)) {
                alert('');
                return false;
            }
        }
        return true;
    }

    private static ValidateIDElementTypeAndLength(udt: SimpleDataTypeDef): boolean {
        return udt.DataType === BaseDataType.String && udt.Length === 36 && udt.Precision === 0;
    }

    private static isNullOrEmptyString(item: string): boolean {
        if (isNullOrUndefined(item)) {
            return true;
        }
        return item.length === 0;
    }

    private static isNullOrEmptyList(items: any[]): boolean {
        if (isNullOrUndefined(items)) {
            return true;
        }
        return items.length === 0;
    }
}



