import { Component, OnInit, ViewChild } from '@angular/core';
import {
  ViewSimpleDataTypeDef, SimpleDataTypeDef, MetadataDto, GSPMetadataService, UdtMetadataContent, ViewComplexDataTypeDef, HttpUrlConst
} from './service/udt-reference';
import { isNullOrUndefined } from 'util';
import { ValidateBeforeSave } from './service/validateBeforeSave';
import { UdtPublishService } from './service/udt-publish.service';
import { UdtTypeConst } from './service/libs/const';
import { ComplexDataTypeDef } from './service/libs/UnifiedDataType/src/ComplexDataTypeDef';
import { TemporaryComplexData, TemporarySimpleData } from './service/templeData';
import { UdtValidationsTabComponent } from './simple-udt-designer/validations-tab/validations-tab.component';
import { UdtChangeIDElementLength } from './service/udt-change-id-length';
import { UdtMetadataService } from './service/udt-metadata.service';
// import { TreeModel } from '../../../../packages/file-tree-panel-package/models/tree-model';

@Component({
  selector: 'app-udt-desinger-frame',
  templateUrl: './udt-desinger-frame.component.html',
  styleUrls: ['./udt-desinger-frame.component.css'],
  providers: [GSPMetadataService, UdtPublishService, UdtMetadataService]
})
export class UdtDesingerFrameComponent implements OnInit {

  private _metadataContnet: MetadataDto;
  udtContentWithType: UdtMetadataContent;

  /**
   * 
   * //
   */
  udtType: string;

  /**
   * 
   */
  showCmpCodeWindow = false;
  cmpGenerateCode: string;

  /**
   * uribe
   */
  set metadataUri(uri: string) {
    const fileName = this.getFileNameFromUri(uri);
    const path = this.getPathFromUri(uri);
    this.lcmService.loadUdtMetadata(fileName, path).subscribe(
      data => {
        this._metadataContnet = data;
        this.loadUdt(data);
      });
  }


  set metadataContent(metadataContent: any) {
    this._metadataContnet = metadataContent;
    this.loadUdt(metadataContent);
  }

  get isComplexDataTypeDef(): boolean {
    return this.udtType === UdtTypeConst.ComplexDataType;
  }

  @ViewChild('validations') validations: UdtValidationsTabComponent;

  /**
   * Tab-()
   */
  public viewSimpleUdt: ViewSimpleDataTypeDef;

  /**
   * Tab-()
   */
  viewComplexUdt: ViewComplexDataTypeDef;

  /**
   * 
   */
  showErrorStackWindow = false;
  errorStack: string;
  errorStackHead: string;
  errorToken = '#GSPBefError#';

  // constructor(private treeModel: TreeModel, private lcmService: GSPMetadataService, private publishService: UdtPublishService) { }
  constructor(private lcmService: UdtMetadataService, private publishService: UdtPublishService) { }

  ngOnInit() {
  }

  /**
   * 
   * @param uri
   */
  getFileNameFromUri(uri: string): string {
    const s = uri.split('/');
    return s[s.length - 1];
  }

  /**
   * 
   * @param uri
   */
  getPathFromUri(uri: string): any {
    return uri.substring(1, uri.lastIndexOf('/'));
  }

  loadSimpleDataTypdDef(udt: SimpleDataTypeDef) {
    // --------------------------------------
    // id22=>36,201810
    UdtChangeIDElementLength.changeSimpleUdtIDElementLength(udt);
    // --------------------------------------

    this.viewSimpleUdt = new ViewSimpleDataTypeDef(udt);
  }

  /**
   * udt
   */
  loadComplexDataTypeDef(complexUdt: ComplexDataTypeDef) {
    // --------------------------------------
    // id22=>36,201810
    UdtChangeIDElementLength.changeComplexUdtIDElementLength(complexUdt);
    // --------------------------------------

    this.viewComplexUdt = new ViewComplexDataTypeDef(complexUdt);
  }

  loadUdt(dto: MetadataDto): any {
    // 
    if (isNullOrUndefined(dto) || isNullOrUndefined(dto.content)) {
      this.clearData();
      return;
    }
    HttpUrlConst.currentPath = dto.relativePath;
    this.udtContentWithType = JSON.parse(dto.content);
    this.udtType = this.udtContentWithType.Type;
    const udt = JSON.parse(this.udtContentWithType.Content);

    switch (this.udtType) {
      case UdtTypeConst.SimpleDataType:
        this.loadSimpleDataTypdDef(udt);
        break;
      case UdtTypeConst.ComplexDataType:
        this.loadComplexDataTypeDef(udt);
        break;
      default:
        alert('Udt' + this.udtType);
    }
  }

  testSimpleUdt() {
    this.udtType = UdtTypeConst.SimpleDataType;
    const udt = TemporarySimpleData;
    this.loadSimpleDataTypdDef(udt);
  }

  testComplexUdt() {
    this.udtType = UdtTypeConst.ComplexDataType;
    const udt = TemporaryComplexData;
    this.loadComplexDataTypeDef(udt);
  }

  private clearData() {
    this.viewSimpleUdt = null;
    this.viewSimpleUdt = null;
  }

  saveMetadata() {
    // ----------------webIDE-------------------------
    let udt;
    if (this.udtType === UdtTypeConst.SimpleDataType) {
      // 
      if (!ValidateBeforeSave.ValidateSimpleDataType(this.viewSimpleUdt.toJS())) {
        return;
      }
      udt = this.viewSimpleUdt.toJS();
      console.log('[udt]');
      console.log(udt);
    } else {
      udt = this.viewComplexUdt.toJS();
      console.log('[udt]');
      console.log(udt);
    }

    const dto = this._metadataContnet;
    this.udtContentWithType.Content = JSON.stringify(udt);
    dto.content = JSON.stringify(this.udtContentWithType);
    this.lcmService.saveUdtMetadata(dto).subscribe(saveResponse => {
      // dbobe
      this.lcmService.loadUdtMetadata(dto.fileName, dto.relativePath).subscribe(
        data => {
          this.loadUdt(data);
          alert('');
        }
      );
    }, error => {
      const head = '';
      const errorString = <string>(error.text ? error.text() : error.text);
      this.handleErrorString(head, errorString);
    });
  }

  /**
   * 
   */
  reloadMetadata() {
    this.lcmService.loadUdtMetadata(this._metadataContnet.fileName, this._metadataContnet.relativePath).subscribe(
      data => {
        this.loadUdt(data);
        alert('');
      }, error => {
        const head = '';
        const errorString = <string>(error.text ? error.text() : error.text);
        this.handleErrorString(head, errorString);
      }
    );
  }

  publishUdt() {
    this.publishService.publishBizEntity(this._metadataContnet.fileName, this._metadataContnet.relativePath).subscribe(data => {
      // udt
      this.lcmService.loadUdtMetadata(this._metadataContnet.fileName, this._metadataContnet.relativePath).subscribe(
        loadData => {
          // cmp
          // this.treeModel.refresh();
          this.loadUdt(loadData);
          alert('');
        }
      );
    }, error => {
      const head = '';
      const errorString = <string>(error.text ? error.text() : error.text);
      this.handleErrorString(head, errorString);
    });
  }

  // -----------------------------------------------------------------------------
  /**
   * 
   */
  showErrorStack(errorHead: string, errorMessage: string) {
    this.errorStackHead = errorHead;
    this.errorStack = errorMessage;
    this.showErrorStackWindow = !this.showErrorStackWindow;
  }

  /**
   * 
   * @param head
   * @param errorString
   */
  handleErrorString(head: string, errorString: string) {
    if (!isNullOrUndefined(errorString) && errorString.includes(this.errorToken)) {
      const strings = errorString.split(this.errorToken);
      const errorMessage = strings[1];
      this.showErrorStack(head, errorMessage);
    } else {
      this.showErrorStack(head, errorString);
    }
  }
  // ---------------------------------------------------------------------------------------
}



