/**
 * 
 */
enum ColumnMapType {
  /**
   * 
   */
  SingleColumn = 'SingleColumn',

  /**
   * 
   */
  MultiColumns = 'MultiColumns',
}

export { ColumnMapType };

export const ColumnMapTypes: Array<{ text: string, value: ColumnMapType }> = [
  { text: '', value: ColumnMapType.MultiColumns },
  { text: '', value: ColumnMapType.SingleColumn }
];



