import { isNullOrUndefined } from 'util';
import {
    UseType, SimpleDataTypeConst, UdtElement, ComplexDataTypeDef, ComplexDataTypeConst,
    UdtTypeConst, UdtMetadataContent, UdtAssociation, GspAssociation, UdtAssociationKey, UdtEnumValue, Guid
} from './libs';
import {
    SimpleDataTypeDef, GSPAssociationKey, GSPEnumValue
} from './udt-reference';

export class UdtUtil {

    /**
     * udt
     * @param element 
     * @param udtContent udt
     */
    static loadUdtProperty(element: UdtElement, udtContent: UdtMetadataContent) {
        if (isNullOrUndefined(udtContent)) {
            alert('');
            return;
        }
        const type = udtContent.Type;

        switch (type) {
            case UdtTypeConst.ComplexDataType:
                // udt,codeName
                const cUdt = <ComplexDataTypeDef>JSON.parse(udtContent.Content);
                this.mapComplexUdtToUdtElement(element, cUdt);
                element.UnifiedDataType = null;
                break;
            case UdtTypeConst.SimpleDataType:
                const sUdt = <SimpleDataTypeDef>JSON.parse(udtContent.Content);
                element.UnifiedDataType = sUdt;
                // udt,be
                this.mapSimpleUdtToUdtElement(element, sUdt);
                break;
        }
    }

    /**
     * udt
     * @param element 
     * @param udtContent udt
     */
    static updateUdtProperty(element: UdtElement, udtContent: UdtMetadataContent) {
        if (isNullOrUndefined(udtContent)) {
            alert('');
            return;
        }
        const type = udtContent.Type;

        switch (type) {
            case UdtTypeConst.ComplexDataType:
                // udt,
                element.UnifiedDataType = null;
                break;
            case UdtTypeConst.SimpleDataType:
                const sUdt = <SimpleDataTypeDef>JSON.parse(udtContent.Content);
                element.UnifiedDataType = sUdt;
                // udt,
                this.updateUdtConstraintPropertyToElement(element, sUdt);
                break;
        }
    }

    /**
     * ()
     * @param element be
     * @param udt 
     */
    private static mapComplexUdtToUdtElement(element: UdtElement, cUdt: ComplexDataTypeDef) {
        // 
        if (this.isEmpty(element.Code)) {
            element.Code = cUdt.Code;
        }
        if (this.isEmpty(element.Name)) {
            element.Name = cUdt.Name;
        }
        // 
        const newElement = new UdtElement();
        element.ObjectType = newElement.ObjectType;
        element.Length = newElement.Length;
        element.Precision = newElement.Precision;
        element.ObjectType = newElement.ObjectType;
        element.DefaultValue = newElement.DefaultValue;
    }

    /**
     * ()udt
     * @param element be
     * @param udt 
     */
    private static mapSimpleUdtToUdtElement(element: UdtElement, udt: SimpleDataTypeDef): any {
        if (this.isEmpty(element.Code)) {
            element.Code = udt.Code;
        }
        if (this.isEmpty(element.Name)) {
            element.Name = udt.Name;
        }
        // []
        // if (isNullOrUndefined(element.LabelId) || element.LabelId.length === 0) {
        //     element.LabelId = udt.Code;
        // }
        element.DataType = udt.DataType;
        element.Length = udt.Length;
        element.Precision = udt.Precision;
        element.ObjectType = udt.ObjectType;
        element.ChildAssociations = this.convertUdtAssociations(udt.ChildAssociations, element);
        element.ContainEnumValues = udt.ContainEnumValues;
        // []udtDesigner/beElement
        element.DefaultValue = udt.DefaultValue;
        element.IsRequire = udt.IsRequired;
    }

    /**
     * 
     * @param assos
     */
    private static convertUdtAssociations(assos: UdtAssociation[], element: UdtElement): GspAssociation[] {
        if (isNullOrUndefined(assos) || assos.length === 0) {
            return null;
        }
        const assoList = new Array<GspAssociation>();
        assos.forEach(udtAsso => {
            assoList.push(this.convertUdtAssociation(udtAsso, element));
        }
        );
        return assoList;
    }

    private static convertUdtAssociation(udtAsso: UdtAssociation, element: UdtElement): GspAssociation {
        const asso = new GspAssociation();
        asso.Id = Guid.newGuid().toString();
        asso.KeyCollection = this.convertUdtAssoKeys(udtAsso.KeyCollection, element);
        asso.RefElementCollection = udtAsso.RefElementCollection;
        asso.RefModelCode = udtAsso.RefEntityCode;
        asso.RefModelID = udtAsso.RefEntityId;
        asso.RefModelName = udtAsso.RefEntityName;
        asso.RefModelPkgName = udtAsso.RefEntityPkgName;
        return asso;
    }

    /**
     * 
     * @param udtEnums
     */
    private static convertUdtEnumValues(udtEnums: UdtEnumValue[]): GSPEnumValue[] {
        if (isNullOrUndefined(udtEnums) || udtEnums.length === 0) {
            return null;
        }
        const enumList = new Array<GSPEnumValue>();
        udtEnums.forEach(udtEnum => {
            enumList.push(this.convertUdtEnumValue(udtEnum));
        }
        );
        return enumList;
    }

    private static convertUdtEnumValue(udtEnum: UdtEnumValue): GSPEnumValue {
        const enumValue = new GSPEnumValue();
        enumValue.Name = udtEnum.Name;
        enumValue.Value = udtEnum.Value;
        return enumValue;
    }

    /**
     * 
     * @param udtKeys
     */
    private static convertUdtAssoKeys(udtKeys: UdtAssociationKey[], element: UdtElement): GSPAssociationKey[] {
        if (isNullOrUndefined(udtKeys) || udtKeys.length === 0) {
            return null;
        }
        const keys = new Array<GSPAssociationKey>();
        udtKeys.forEach(udtKey => {
            keys.push(this.convertUdtAssoKey(udtKey, element));
        });
        return keys;
    }

    private static convertUdtAssoKey(udtKey: UdtAssociationKey, element: UdtElement): GSPAssociationKey {
        const key = new GSPAssociationKey();
        key.SourceElement = udtKey.SourceElement;
        key.SourceElementDisplay = udtKey.SourceElementDisplay;
        key.TargetElement = element.Id;
        key.TargetElementDisplay = element.Name;
        return key;
    }

    /**
     * ()
     * @param element be
     * @param udt 
     */
    private static updateUdtConstraintPropertyToElement(element: UdtElement, udt: SimpleDataTypeDef): UdtElement {
        const infos = udt.PropertyUseTypeInfos;
        if (isNullOrUndefined(infos) || infos.length === 0) {
            return;
        }
        infos.forEach(info => {
            if (info.PropertyUseType === UseType.AsConstraint) {
                this.setPropertyValue(info.PropertyName, element, udt);
            }
        });
    }

    /**
     * 
     * @param propertyName 
     * @param udt udt
     */
    static canPropertyBeEdit(propertyName: string, udt: any): boolean {
        const udtPropertyName = propertyName;
        if (isNullOrUndefined(udt)) {
            return this.canComplexUdtPropertyBeEdit(udtPropertyName);
        } else {
            return this.canSimpleUdtPropertyBeEdit(udtPropertyName, <SimpleDataTypeDef>udt);
        }
    }

    /**
     * udt
     * @param propertyName 
     */
    private static canComplexUdtPropertyBeEdit(propertyName: string): boolean {
        switch (propertyName) {
            case ComplexDataTypeConst.Code:
            case ComplexDataTypeConst.Name:
                return true;
            default: return false;
        }
    }

    /**
     * udtinfos
     * @param propertyName 
     * @param sUdt udt
     */
    private static canSimpleUdtPropertyBeEdit(propertyName: string, sUdt: SimpleDataTypeDef): boolean {
        //  
        if (isNullOrUndefined(propertyName)) {
            return true;
        }
        //  [][][]
        if (propertyName === SimpleDataTypeConst.DataType) {
            return true;
        }
        //  be
        const infos = sUdt.PropertyUseTypeInfos;
        const info = infos.find(item => item.PropertyName === propertyName);
        if (isNullOrUndefined(info)) {
            // alert(`[${udtPropertyName}](/)`);
            return true;
        }
        // ,
        return info.PropertyUseType === UseType.AsTemplate;
    }

    /**
     * 
     * @param propertyName 
     * @param element udt
     * @param udt 
     */
    private static setPropertyValue(propertyName: string, element: UdtElement, udt: SimpleDataTypeDef): any {
        switch (propertyName) {
            case SimpleDataTypeConst.DataType:
                element.DataType = udt.DataType;
                break;
            case SimpleDataTypeConst.Length:
                element.Length = udt.Length;
                break;
            case SimpleDataTypeConst.Precision:
                element.Precision = udt.Precision;
                break;
            case SimpleDataTypeConst.ObjectType:
                element.ObjectType = udt.ObjectType;
                break;
            case SimpleDataTypeConst.ChildAssociations:
                element.ChildAssociations = this.convertUdtAssociations(udt.ChildAssociations, element);
                break;
            case SimpleDataTypeConst.ContainEnumValues:
                element.ContainEnumValues = udt.ContainEnumValues;
                break;
            case SimpleDataTypeConst.DefaultValue:
                element.DefaultValue = udt.DefaultValue;
                break;
            case ComplexDataTypeConst.IsRequire:
                element.IsRequire = udt.IsRequired;
                break;
        }
    }

    /**
     * /
     * @param testString array
     */
    private static isEmpty(testString: any): boolean {
        if (isNullOrUndefined(testString) || testString.length === 0) {
            return true;
        }
        return false;
    }
}



