import { List, Map } from 'immutable';
import { isNullOrUndefined } from 'util';
import { ListBase } from '../base/listBase';
import { MapBase } from '../base/mapBase';
import { UnifiedDataTypeConst } from '../../../const';
import { CommonDetermination } from '../../ref';

export class ViewDeterminationList extends ListBase<ViewDetermination> {

  identifier = 'ID';

  constructor(validationInfoArray: CommonDetermination[] | List<any>) {
    super(validationInfoArray);
    if (isNullOrUndefined(validationInfoArray)) {
      return;
    }
    this.data = this.getViewDeterminations(this.data);
  }

  getViewDeterminations(validationInfoArray: List<any>): any {
    if (isNullOrUndefined(validationInfoArray)) {
      return;
    }

    let viewEnumValueList = validationInfoArray;

    if (validationInfoArray.size > 0) {
      for (let i = 0; i < validationInfoArray.size; i++) {
        viewEnumValueList = viewEnumValueList.update(i, () => this.getViewDetermination(validationInfoArray.get(i)));
      }
      return viewEnumValueList;
    }
  }

  getViewDetermination(validationInfo: CommonDetermination): any {
    return new ViewDetermination(validationInfo);
  }
}

export class ViewDetermination extends MapBase {
  identifier = 'ID';

  /**
   * 
   * @param element
   */
  constructor(enumValue: CommonDetermination | Map<string, any>) {
    super(enumValue);
  }

  get Id(): string {
    return this.get(this.identifier);
  }


  get Code(): string {
    return this.get(UnifiedDataTypeConst.Code);
  }
  set Code(value: string) {
    this.set(UnifiedDataTypeConst.Code, value);
  }

  get Name(): string {
    return this.get(UnifiedDataTypeConst.Name);
  }
  set Name(value: string) {
    this.set(UnifiedDataTypeConst.Name, value);
  }

  get Description(): string {
    return this.get(UnifiedDataTypeConst.Description);
  }
  set Description(value: string) {
    this.set(UnifiedDataTypeConst.Description, value);
  }

  get RequestElements(): ListBase<any> {
    return this.get(UnifiedDataTypeConst.RequestElements);
  }
  set RequestElements(value: ListBase<any>) {
    this.set(UnifiedDataTypeConst.RequestElements, value);
  }
}




