
import { Component, Input, Output, EventEmitter, ViewChild, OnInit, OnChanges, SimpleChanges, OnDestroy } from '@angular/core';
import { Validators, FormGroup, FormControl } from '@angular/forms';
import { isNullOrUndefined } from 'util';
import { Injectable } from '@angular/core';

import { AssociationRefElementGirdComponent } from '../association-refElement-grid/association-refElement-grid.component';
import { AssociationElementGirdComponent } from '../association-element-gird/association-element-gird.component';
import {
    GSPBusinessEntity, SimpleDataTypeDef, UdtAssociation,
    ViewAssociation, GSPAssociation, GSPAssociationKey, Metadata4RefDto, HttpUrlConst
} from '../../../../service/udt-reference';
import { BeRefMetadataHttpService } from '../../../../service/lcm-service/beRefMetadataHttpService';
import { UdtMetadataService } from '../../../../service/udt-metadata.service';
import { MetadataDto } from '@gsp-lcm/metadata-selector/metadata/metadata4refdto';

/**
 * -----------------2-------------------------
 */

/**
 * 6.1
 * ContainElementsContainElements
 * ContainElements
 * ##  
 */
@Component({
    selector: 'app-association-edit-form',
    styleUrls: ['./association-edit-form.component.css'],
    templateUrl: './association-edit-form.component.html',
    providers: [UdtMetadataService]
})
export class AssoKeyEditFormComponent implements OnInit, OnChanges, OnDestroy {

    public refModel: GSPBusinessEntity;
    active = false;
    showLoadMetadataWindow = false;

    @Input() public isNew: boolean;
    // @Input() public currentObject: GSPBizEntityObject;
    @Input() public currentElement: SimpleDataTypeDef;
    @Input() public currentAsso: UdtAssociation;

    @Output() confirmAsso: EventEmitter<ViewAssociation> = new EventEmitter();
    @Output() editAsso: EventEmitter<ViewAssociation> = new EventEmitter();

    // @ViewChild('assoElementGrid') assoElementGrid: AssociationElementGirdComponent;
    @ViewChild('refElementGrid') refElementGrid: AssociationRefElementGirdComponent;

    ngOnDestroy(): void {
    }

    ngOnChanges(changes: SimpleChanges): void {
        if (changes['currentAsso']) {
            this.refresh();
        }
    }

    ngOnInit(): void {
        // this.dataService.getRefModel.subscribe(data => {
        //     this.loadRefModel(data);
        //     return data;
        // });
    }
    constructor(private dataService: UdtMetadataService) {

    }

    /**
     * 
     */
    get currentObjectName(): string {
        return '';
        // return this.currentObject.Name;
    }

    /**
     * 
     */
    get refModelName(): string {
        if (this.currentAsso && this.currentAsso.RefEntityName) {
            return this.currentAsso.RefEntityName;
        } else {
            return '';
        }
    }

    /**
     * (SourceElement)
     */
    get currentElementName(): string {
        if (this.currentElement && this.currentElement.Name) {
            const eleName = this.currentElement.Name;
            if (this.currentAsso && this.currentAsso.KeyCollection && this.currentAsso.KeyCollection.length !== 0) {
                this.currentAsso.KeyCollection[0].TargetElementDisplay = eleName;
            }
            return eleName;
        } else {
            return '';
        }
    }

    /**
     * (TargetElement)
     */
    get refElementName(): string {
        if (this.currentAsso && this.currentAsso.KeyCollection && this.currentAsso.KeyCollection.length !== 0) {
            const key = this.currentAsso.KeyCollection[0];
            return key.SourceElementDisplay;
        } else {
            return '';
        }
    }

    /**
     * modelID
     */
    getRefModelByModelID(currentAsso: UdtAssociation): GSPBusinessEntity {
        if (isNullOrUndefined(currentAsso)) {
            this.refModel = null;
            return null;
        } else {
            const modelId = currentAsso.RefEntityId;
            const pkgName = currentAsso.RefEntityPkgName;
            if (isNullOrUndefined(modelId)) {
                this.loadRefModel(null);
                return null;
            } else {
                this.dataService.GetRefMetadata(HttpUrlConst.currentPath, modelId).subscribe(data => {
                    const meta = data as MetadataDto;
                    const be = JSON.parse(meta.content) as GSPBusinessEntity;
                    this.loadRefModel(be);
                }, error => alert(''));
            }
        }
    }

    loadRefModel(refBizEntity: GSPBusinessEntity) {
        this.refModel = refBizEntity;
        if (isNullOrUndefined(this.refElementGrid)) {
            return;
        }
        this.refElementGrid.refresh(refBizEntity);
    }

    /**
     * be
     */
    selectRefModel() {
        this.showLoadMetadataWindow = !this.showLoadMetadataWindow;
    }

    /**
     * be
     * @param beMetadata
     */
    GetBeMetadata(beMetadata: Metadata4RefDto) {
        if (isNullOrUndefined(beMetadata)) {
            return;
        }
        if (beMetadata.packageHeader) {
            this.currentAsso.RefEntityPkgName = beMetadata.packageHeader.name;
        } else {
            this.currentAsso.RefEntityPkgName = '';
        }
        const be = JSON.parse(beMetadata.metadata.content) as GSPBusinessEntity;

        // 
        this.currentAsso.RefEntityId = beMetadata.metadata.id;
        this.currentAsso.RefEntityCode = be.Code;
        this.currentAsso.RefEntityName = be.Name;

        const idElement = be.MainObject.ContainElements.find(ele => ele.ID === be.MainObject.ColumnGenerateID.ElementID);
        // key
        const newAssoKey = new GSPAssociationKey();
        newAssoKey.SourceElement = idElement.ID;
        newAssoKey.SourceElementDisplay = idElement.Name;
        newAssoKey.TargetElement = this.currentElement.Id;
        newAssoKey.TargetElementDisplay = this.currentElement.Name;
        this.currentAsso.KeyCollection = [newAssoKey];
        this.loadRefModel(be);
    }


    /**
     * 
     */
    refresh() {
        if (!this.currentAsso) {
            this.refModel = null;
            return;
        }
        this.getRefModelByModelID(this.currentAsso);
    }

    // /**
    //  * 
    //  */
    // public onConfirm(): void {
    //     this.currentAsso.RefModelName = this.refModel.Name;
    //     this.currentAsso.RefModelCode = this.refModel.Code;
    //     this.refElementGrid.confirm();
    // }

    hasRefModel(): boolean {
        return !isNullOrUndefined(this.refModel);
    }
}



