import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { SelectMetadataInBeComponent } from './selectmetadate/selectmetadata.component';
import { ModalModule } from '@farris/ui/container/modal/modal';
import { FormsModule } from '@angular/forms';
import { TreeViewModule } from '@progress/kendo-angular-treeview';
import { TreeModule, TreeTableModule } from 'primeng/primeng';
import { GridModule } from '@progress/kendo-angular-grid';
import { InputTextModule } from 'primeng/inputtext';
import { MetadataService } from '@gsp-lcm/metadata-selector/metadata/metadata-select/metadata.service';
import { FarrisDialogModule } from '@farris/ui';
import { MetadataSelectModule, GSPMetadataServiceModule } from '@gsp-lcm/metadata-selector';
import { HttpUrlConst } from '../../service/udt-reference';

@NgModule({
  imports: [
    CommonModule,
    ModalModule.forRoot(),
    FormsModule,
    TreeViewModule,
    TreeModule,
    TreeTableModule,
    GridModule,
    InputTextModule,
    FarrisDialogModule,
    MetadataSelectModule,
    // url
    GSPMetadataServiceModule.forRoot(HttpUrlConst.currentUrl)
  ],
  exports: [
    SelectMetadataInBeComponent
  ],
  providers: [MetadataService], // 
  declarations: [SelectMetadataInBeComponent]
})
export class MetadataModule { }



